import React, { useEffect, useState } from "react";
import Layout from "../../components/Layout";
import { Form, Input, Row, Col, message, Select, DatePicker } from "antd";

import "../../styles/Newtechpagestyles.css";

import { useParams, useNavigate } from "react-router-dom";
import axios from "axios";
import { useDispatch, useSelector } from "react-redux";
import { setRole } from "../../redux/features/roleslice";
const { Option } = Select;

const Serverdetail = () => {
  const [serverdetails, setServerdetails] = useState([]);
  const [itemtype, setItemtype] = useState();
  //const [formattedDates, setFormattedDates] = useState({});
  const params = useParams();
    const [serverroomname,setServerroomname]=useState();
    const [serveritems,setServeritems]=useState([]);
  const navigate = useNavigate();

  const roles = useSelector((State) => State.role.role);
  const dispatch = useDispatch();
  
    useEffect(() => {
      if (roles === "admin" || roles === "editor") {
        localStorage.setItem("role", roles);
      }
    }, [roles]);
  
    useEffect(() => {
      const savedRole = localStorage.getItem("role");
      if (savedRole && savedRole !== roles) {
        dispatch(setRole(savedRole)); // Sync Redux with localStorage
      }
    }, [dispatch]);

    
  const getalllabs = async () => {
    try {
      const res = await axios.get("/api/v1/tech/server/getallserver", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });
      if (res.data.success) {
        message.success(res.data.message);
        setServeritems(res.data.data);
      } else {
        message.error(res.data.messgae);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  useEffect(()=>{
    const lab=serveritems.find((data)=>data._id===params.serverroomid);
     if(lab){
      setServerroomname(lab.roomname);
     }
      },[serveritems,params.serverroomid]);

   useEffect(() => {
      getalllabs();
    }, []);


  const handlefinish = async (values) => {
    // console.log("Received values:", values);
    values.serverroomid = params.serverroomid;
    if (values.itemtype === "other" && values.otheritemtype) {
      values.itemtype = values.otheritemtype;
      delete values.otheritemtype;
    }
    try {
      const res = await axios.post(
        "/api/v1/tech/server/addserverdetail",
        values
      );
      if (res.data.success) {
        message.success(res.data.message);
      } else {
        message.error(res.data.message);
      }
      window.location.reload();
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  const getdetails = async () => {
    try {
      const res = await axios.post(
        "/api/v1/tech/server/getdetailofserver",
        {
          serverroomid: params.serverroomid,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      if (res.data.success) {
        message.success(res.data.message);
        setServerdetails(res.data.data);
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };
  useEffect(() => {
    getdetails();
  }, []);

  const handledelete = async (id) => {
    try {
      const res = await axios.post(
        "/api/v1/tech/server/deleteserverdetail",
        {
          id,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      if (res.data.success) {
        message.success(res.data.message);
        window.location.reload();
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };
  return (
    <>
      <Layout>
          <div style={{alignItems:'center',justifyContent:'center',display:'flex',fontSize:'34px',fontFamily:'bold'}}><p>{serverroomname}</p></div>
               <div
                 className="form-div"
                 style={{
                   border: "1px solid black",
                   width: "100%",
                   float: "left",
                   margin: "2px",
                 }}
               >
                 <Form layout="vertical" onFinish={handlefinish}>
                   <h5 className="text-center">Add Item</h5>
                   <Row Row gutter={20}>
                     <Col xs={24} md={24} lg={5}>
                       <Form.Item
                         name="itemtype"
                         label="Item Type"
                         rules={[{ required: true, message: "Item type is required" }]}
                       >
                         <Select
                           placeholder="select item type"
                           onChange={(value) => setItemtype(value)}
                         >
                    <option value="server">Server</option>
                    <option value="desktop">Desktop</option>
                    <option value="laptop">Laptop</option>
                    <option value="networkSwitches">Network Switches</option>
                    <option value="routers">Routers</option>
                    <option value="firewalls">Firewalls</option>
                    <option value="ups">
                      Uninterruptible Power Supply (UPS)
                    </option>
                    <option value="coolingSystems">
                      Cooling Systems (HVAC)
                    </option>
                    <option value="patchPanels">Patch Panels</option>
                    <option value="cableManagement">
                      Cable Management Systems
                    </option>
                    <option value="storageArrays">Storage Arrays</option>
                    <option value="kvmSwitch">
                      KVM Switch (Keyboard, Video, Mouse)
                    </option>
                    <option value="serverRacks">Server Racks/Cabinets</option>
                    <option value="securityCameras">Security Cameras</option>
                    <option value="accessControl">
                      Access Control Systems
                    </option>
                    <option value="environmentalMonitoring">
                      Environmental Monitoring Systems
                    </option>
                    <option value="backupSystems">Backup Systems</option>
                    <option value="fireSuppression">
                      Fire Suppression Systems
                    </option>
                    <option value="remoteManagement">
                      Remote Management Tools
                    </option>
                    <option value="pdus">
                      Power Distribution Units (PDUs)
                    </option>
                    <option value="patchCables">Patch Cables</option>
                    <option value="monitoringSoftware">
                      Monitoring and Alerting Software
                    </option>
                    <option value="other">Other</option>
                  </Select>
                </Form.Item>
              </Col>
              {itemtype === "other" && (
                <Col xs={24} md={24} lg={5}>
                  <Form.Item
                    label="other item name"
                    name="otheritemtype"
                    rules={[
                      { required: true, message: "other item name required" },
                    ]}
                  >
                    <Input type="text" placeholder="type item name" />
                  </Form.Item>
                </Col>
              )}
              <Col xs={24} md={24} lg={5}>
                <Form.Item
                  name="installationdate"
                  label="Installation Date"
                  rules={[
                    {
                      required: true,
                      message: "Installation date is required",
                    },
                  ]}
                >
                  <DatePicker style={{ width: "100%" }} />
                </Form.Item>
              </Col>
              <Col xs={24} md={24} lg={5}>
                <Form.Item
                  name="description"
                  label="Description"
                  rules={[
                    { required: true, message: "Description is required" },
                  ]}
                >
                  <Input.TextArea
                    placeholder="enter description"
                    rows={1}
                    style={{ width: "100%" }}
                  />
                </Form.Item>
              </Col>
              {itemtype === "server" && (
                <>
                  <Col xs={24} md={24} lg={5}>
                    <Form.Item
                      label="Server Name/ID"
                      name="serverName"
                      rules={[
                        {
                          required: true,
                          message: "Server Name/ID is required",
                        },
                      ]}
                    >
                      <Input
                        type="text"
                        placeholder="Enter server name or ID"
                      />
                    </Form.Item>
                  </Col>

                  <Col xs={24} md={24} lg={5}>
                    <Form.Item
                      label="Server Type/Role"
                      name="serverType"
                      rules={[
                        {
                          required: true,
                          message: "Server Type/Role is required",
                        },
                      ]}
                    >
                      <Select placeholder="Select server type/role">
                        <Option value="database">Database Server</Option>
                        <Option value="web">Web Server</Option>
                        <Option value="application">Application Server</Option>
                        <Option value="file">File Server</Option>
                        <Option value="backup">Backup Server</Option>
                      </Select>
                    </Form.Item>
                  </Col>

                  <Col xs={24} md={24} lg={5}>
                    <Form.Item
                      label="Operating System"
                      name="operatingSystem"
                      rules={[
                        {
                          required: true,
                          message: "Operating System is required",
                        },
                      ]}
                    >
                      <Select placeholder="Select operating system">
                        <Option value="linux">Linux</Option>
                        <Option value="windows">Windows Server</Option>
                        <Option value="macos">macOS</Option>
                        <Option value="ubuntu">Ubuntu</Option>
                        <Option value="centos">CentOS</Option>
                      </Select>
                    </Form.Item>
                  </Col>

                  <Col xs={24} md={24} lg={5}>
                    <Form.Item
                      label="IP Address"
                      name="ipaddress"
                      rules={[
                        { required: true, message: "IP Address is required" },
                      ]}
                    >
                      <Input type="text" placeholder="Enter IP address" />
                    </Form.Item>
                  </Col>

                  <Col xs={24} md={24} lg={5}>
                    <Form.Item
                      label="Host Name"
                      name="hostname"
                      rules={[
                        { required: true, message: "Host Name is required" },
                      ]}
                    >
                      <Input type="text" placeholder="Enter host name" />
                    </Form.Item>
                  </Col>

                  <Col xs={24} md={24} lg={5}>
                    <Form.Item
                      label="MAC Address"
                      name="macAddress"
                      rules={[
                        { required: true, message: "MAC Address is required" },
                      ]}
                    >
                      <Input type="text" placeholder="Enter MAC address" />
                    </Form.Item>
                  </Col>
                </>
              )}
              {itemtype === "desktop" && (
                 <>
                  
                  <Col xs={24} md={24} lg={5}>
                    <Form.Item
                      name="desktopnumber"
                      label="Desktop Number"
                      rules={[
                        {
                          required: true,
                          message: "Desktop number is required",
                        },
                      ]}
                    >
                      <Input
                        type="text"
                        style={{ width: "100%" }}
                        placeholder="enter desktop number"
                      />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24} lg={5}>
                    <Form.Item
                      name="ipaddress"
                      label="Ip Address"
                      rules={[
                        {
                          required: true,
                          message: "ip address is required",
                        },
                      ]}
                    >
                      <Input
                        type="text"
                        style={{ width: "100%" }}
                        placeholder="enter ip address "
                      />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24} lg={5}>
                    <Form.Item
                      name="monitornumber"
                      label="Monitor Number"
                      rules={[
                        {
                          required: true,
                          message: "Monitor number is required",
                        },
                      ]}
                    >
                      <Input type="text" placeholder="enter monitor number" />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24} lg={5}>
                    <Form.Item
                      name="monitorname"
                      label="Monitor Attributes"
                      rules={[{ required: true }]}
                    >
                      <Input type="text" placeholder="Brand, Model, Size" />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24} lg={5}>
                    <Form.Item
                      name="mousenumber"
                      label="Mouse Number"
                      rules={[
                        { required: true, message: "Mouse number is required" },
                      ]}
                    >
                      <Input type="text" placeholder="enter mouse number" />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24} lg={5}>
                    <Form.Item
                      name="mousename"
                      label="Mouse Attributes"
                      rules={[{ required: true }]}
                    >
                      <Input type="text" placeholder="Brand and Model" />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24} lg={5}>
                    <Form.Item
                      name="keyboardnumber"
                      label="Keyboard Number"
                      rules={[
                        { required: true, message: "keyboard no required" },
                      ]}
                    >
                      <Input type="text" placeholder="enter keyboard number" />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24} lg={5}>
                    <Form.Item
                      name="keyboardname"
                      label="Keyboard Attributes"
                      rules={[{ required: true }]}
                    >
                      <Input type="text" placeholder="Brand and model" />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24} lg={5}>
                    <Form.Item
                      name="cpunumber"
                      label="CPU Number"
                      rules={[
                        { required: true, message: "CPU number is required" },
                      ]}
                    >
                      <Input type="text" placeholder="enter cpu number" />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24} lg={5}>
                    <Form.Item
                      name="cpuname"
                      label="CPU Attributes"
                      rules={[{ required: true }]}
                    >
                      <Input type="text" placeholder="Brand, Model, Speed" />
                    </Form.Item>
                  </Col>
                 </>
         
              )}

              <Col xs={24} md={24} lg={5}>
                <Form.Item>
                  <button
                    type="submit"
                    className="btn btn-primary"
                    style={{ width: "100%", marginLeft: "0px",marginTop:"25px" }}
                  >
                    Submit
                  </button>
                </Form.Item>
              </Col>
            </Row>
          </Form>
        </div>

        <table
          className="table"
          style={{
            width: "100%",
            borderCollapse: "collapse",
            marginTop: "10px",
          }}
        >
          <thead>
            <tr>
              <th
                style={{
                  border: "2px solid black",
                  textAlign: "center",
                  padding: "10px",
                  color: "red",
                }}
              >
                Item Type
              </th>
              <th
                style={{
                  border: "2px solid black",
                  textAlign: "center",
                  padding: "10px",
                  color: "red",
                }}
              >
                Installation Date
              </th>
              <th
                style={{
                  border: "2px solid black",
                  textAlign: "center",
                  padding: "10px",
                  color: "red",
                }}
              >
                Description
              </th>
              <th
                style={{
                  border: "2px solid black",
                  textAlign: "center",
                  padding: "10px",
                  color: "red",
                }}
              >
                Desktop/Server Info
              </th>
              <th
                style={{
                  border: "2px solid black",
                  textAlign: "center",
                  padding: "10px",
                  color: "red",
                }}
              >
                Actions
              </th>
            </tr>
          </thead>
          <tbody>
            {serverdetails &&
              serverdetails.map((data) => (
                <tr key={data._id}>
                  <td
                    style={{
                      border: "2px solid black",
                      textAlign: "center",
                      padding: "10px",
                    }}
                  >
                    <b>{data.itemtype}</b>
                  </td>
                  <td
                    style={{
                      border: "2px solid black",
                      textAlign: "center",
                      padding: "10px",
                    }}
                  >
                    {new Date(data.installationdate).toLocaleDateString()}
                  </td>
                  <td
                    style={{
                      border: "2px solid black",
                      textAlign: "center",

                      width: "350px",
                    }}
                  >
                    <div style={{ maxHeight: "150px", overflowY: "auto" }}>
                      {data.description || "N/A"}
                    </div>
                  </td>
                  <td
                    style={{
                      border: "2px solid black",
                      textAlign: "center",
                    }}
                  >
                    {data.itemtype === "server" && (
                      <div style={{ maxHeight: "150px", overflowY: "auto" }}>
                       
                        <p>
                          <b>Server Name/ID: </b>
                          {data.serverName}
                        </p>
                        <p>
                          <b>Server Type: </b>
                          {data.serverType}
                        </p>
                        <p>
                          <b>Operating System: </b>
                          {data.operatingSystem}
                        </p>
                        <p>
                          <b>IP Address: </b>
                          {data.ipaddress}
                        </p>
                        <p>
                          <b>Hostname: </b>
                          {data.hostname}
                        </p>
                        <p>
                          <b>MAC Address: </b>
                          {data.macAddress}
                        </p>
                      </div>
                    )}
                    {data.itemtype === "desktop" && (
                      <div
                        className="desktop-info"
                        style={{
                          maxHeight: "150px",
                          overflowY: "auto",
                        }}
                      >
                        <p>
                          <b>Desktop Number:</b> {data.desktopnumber}
                        </p>
                        <p>
                          <b>IP Address:</b> {data.ipaddress}
                        </p>
                        <p>
                          <b>Monitor Number:</b> {data.monitornumber}
                        </p>
                        <p>
                          <b>Monitor Name:</b> {data.monitorname}
                        </p>
                        <p>
                          <b>Mouse Number:</b> {data.mousenumber}
                        </p>
                        <p>
                          <b>Mouse Name:</b> {data.mousename}
                        </p>
                        <p>
                          <b>Keyboard Number:</b> {data.keyboardnumber}
                        </p>
                        <p>
                          <b>Keyboard Name:</b> {data.keyboardname}
                        </p>
                        <p>
                          <b>CPU Number:</b> {data.cpunumber}
                        </p>
                        <p>
                          <b>CPU Name:</b> {data.cpuname}
                        </p>
                      </div>
                    )}
                  </td>
                  <td
                    style={{
                      border: "2px solid black",
                      textAlign: "center",
                      padding: "10px",
                    }}
                  >
                    <i
                      className="fa-solid fa-trash icon"
                      style={{ cursor: "pointer", margin: "10px" }}
                      onClick={() => {
                        if (window.confirm("Do you want to delete?")) {
                          handledelete(data._id);
                        }
                      }}
                    ></i>
                    <i
                      className="fa-solid fa-pen-to-square edit"
                      style={{ cursor: "pointer", margin: "10px" }}
                      onClick={() => {
                        if (window.confirm("Do you want to edit?")) {
                          navigate(`/serverdetailedit/${data._id}`);
                        }
                      }}
                    ></i>
                  </td>
                </tr>
              ))}
          </tbody>
        </table>
      </Layout>
    </>
  );
};

export default Serverdetail;
