import React, { useEffect, useState } from "react";
import Layout2 from "../../components/Layout2";
import { message, Select, Table } from "antd";
import axios from "axios";

const { Option } = Select;
const Teachersumnon = () => {

     const [teachers, setTeachers] = useState([]);
      const [teacherdetails, setTeacherdetails] = useState([]);
    
      const [teachernames, setTeachernames] = useState({});
    const [totalteachers,setTotalteachers]=useState([]);
    const[teachernums,setTeachernums]=useState('all');
    const[sortorder,setSortorder]=useState('oldtonew')
    
      const [item, setItem] = useState("all");
      const [displaydata, setDisplaydata] = useState();

      const getallteachers = async () => {
        try {
          const res = await axios.get("/api/v1/tech/teacher/getallteacher", {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          });
          if (res.data.success) {
            message.success(res.data.message);
            setTeachers(res.data.data);
          } else {
            message.error(res.data.messgae);
          }
        } catch (error) {
          console.log(error);
          message.error("something went wrong");
        }
      };

      const getteacherdetails = async () => {
        try {
          const res = await axios.get("/api/v1/tech/teacher/getallteacherdetailsnon", {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          });
          if (res.data.success) {
            message.success(res.data.message);
            setTeacherdetails(res.data.data);
          } else {
            message.error(res.data.message);
          }
        } catch (error) {
          console.log(error);
          message.error("something went wrong");
        }
      };

      const teachernamess = async () => {
        const teachernames = {};
     
        const allteacherdetails = teacherdetails;
        for (let data of allteacherdetails) {
          teachernames[data._id] = await teachername(data.teacherid);
        }
        setTeachernames(teachernames);
      };
    
      const teachername = async (teacherid) => {
        const allteachers = teachers;
        for (let data of allteachers) {
          if (data._id === teacherid) {
            return data.teachername;
          }
        }
      };

         useEffect(() => {
            getteacherdetails();
          }, []);
        
          useEffect(() => {
            getallteachers();
          }, []);
        
          useEffect(() => {
            teachernamess();
          }, [teacherdetails, teachers]);


    useEffect(()=>{
  const allteachers=[...new Set(teachers.map(data=>data.teachername))];
  setTotalteachers(allteachers);
    },[teachers]);


    const initialprint = async () =>  {
          if (item === "all") {
            const tabledatas = teacherdetails.map((data) => ({
              key: data._id,
              product:data.itemtype, 
             uniqueid:data.uid,
              date: new Date(data.installationdate).toLocaleDateString(),
              description:data.description,
              teachername: teachernames[data._id],
              
              
               
            }));
            setDisplaydata(tabledatas);
          }
        };
        useEffect(() => {
          initialprint();
        }, [teacherdetails, teachernames]);


        const tabledata = (item, teachername,order) => {
            let displaydatas;
            if (item === 'other') {
              if (teachername === 'all') {
                
                const excludeitem = ["whiteboard", "marker", "chalk", "duster", "notice_board", "bookshelf", "clock", "chair", "desk", 
  "pen_holder", "paper_stack", "file_cabinet", "cupboard", "curtains", "trash_bin", "water_bottle", 
  "notepad", "globe", "poster", "calendar", "plant", "student_chair", "bulletin_board", "stapler", 
  "scissors", "paperweight", "nameplate", "map", "lamp", "cup", "rubber_band", "folder", "sticky_notes", 
  "paper_clips", "first_aid_kit"
                   ];
                displaydatas = teacherdetails.filter(
                  (data) => !excludeitem.includes(data.itemtype)
                );
              } else {
                const excludeitem = [ 
         "whiteboard", "marker", "chalk", "duster", "notice_board", "bookshelf", "clock", "chair", "desk", 
  "pen_holder", "paper_stack", "file_cabinet", "cupboard", "curtains", "trash_bin", "water_bottle", 
  "notepad", "globe", "poster", "calendar", "plant", "student_chair", "bulletin_board", "stapler", 
  "scissors", "paperweight", "nameplate", "map", "lamp", "cup", "rubber_band", "folder", "sticky_notes", 
  "paper_clips", "first_aid_kit"
        ];
                displaydatas = teacherdetails.filter(
                  (data) =>
                    !excludeitem.includes(data.itemtype) &&
                    teachernames[data._id] === teachername
                );
              }
            } else if (item === "all") {
              if (teachername === 'all') {
                displaydatas = teacherdetails;  
              } else {
                displaydatas = teacherdetails.filter(
                  (data) => teachernames[data._id] === teachername
                );
              }
            } else {
              if (teachername === 'all') {
                displaydatas = teacherdetails.filter((data) => data.itemtype === item);
              } else {
                displaydatas = teacherdetails.filter(
                  (data) => data.itemtype === item && teachernames[data._id] ===teachername
                );
              }
            }
          
        displaydatas.sort((a,b)=>{
          const dateA=new Date(a.installationdate);
          const dateB=new Date(b.installationdate);
          return order==='newtoold'?dateB-dateA:dateA-dateB;
        });
        
            const tabledatas = displaydatas.map((data) => ({
                key: data._id,
                product:data.itemtype, 
               uniqueid:data.uid,
                date: new Date(data.installationdate).toLocaleDateString(),
                description:data.description,
                teachername: teachernames[data._id],
            }));
            setDisplaydata(tabledatas);  
          };

          const columns =
            teachernums==="all"?[
            {
                title: "ITEM NAME",
                dataIndex: "product",
                key: "product",
              },
              {
                title:"UNIQUE ID",
                dataIndex:"uniqueid",
                key:"uniqueid"
              },
              { title: "INSTALLATION DATE", dataIndex: "date", key: "date" },
              {
                title:"DESCRIPTION",
                dataIndex: "description",
                key: "description",
              },{
                title:"TEACHER NAME",
                dataIndex:"teachername",
                key:"teachername"
              }
          ]:[
            {
                title: "ITEM NAME",
                dataIndex: "product",
                key: "product",
              },
              {
                title:"UNIQUE ID",
                dataIndex:"uniqueid",
                key:"uniqueid"
              },
              { title: "INSTALLATION DATE", dataIndex: "date", key: "date" },
              {
                title:"DESCRIPTION",
                dataIndex: "description",
                key: "description",
              }
          ];

            const itemchange=(value)=>{
            setItem(value);
            //tabledata(item,labnums);
            }
            
            const teachernamechange=(value)=>{
              setTeachernums(value);
             // tabledata(item,labnums);
            }
            useEffect(()=>{
            tabledata(item,teachernums,sortorder);
            },[item,teachernums,sortorder])
        
  return (
    <Layout2>
       <div style={{alignItems:'center',justifyContent:'center',display:'flex',fontSize:'24px',fontFamily:'bold'}}><p>TEACHERS' ROOM SUMMARY</p></div>
 <div className="d-flex justify-content-center align-items-center gap-2" style={{width:"100%"}}>
 <span>SELECT ITEM : </span>
          <Select placeholder="choose an item" onChange={itemchange}  style={{width:"150px"}}>
          
  {["all","other", "whiteboard", "marker", "chalk", "duster", "notice_board", "bookshelf", "clock", "chair", "desk", 
  "pen_holder", "paper_stack", "file_cabinet", "cupboard", "curtains", "trash_bin", "water_bottle", 
  "notepad", "globe", "poster", "calendar", "plant", "student_chair", "bulletin_board", "stapler", 
  "scissors", "paperweight", "nameplate", "map", "lamp", "cup", "rubber_band", "folder", "sticky_notes", 
  "paper_clips", "first_aid_kit"].map((data)=>(
<Option value={data}>{data}</Option>
  ))}
          </Select>
          <span>SELECT TEACHER : </span>
           <Select placeholder="choose teacher name" onChange={teachernamechange} style={{width:"150px"}}>
           <Option value='all' >All</Option>
           { totalteachers.map((data)=>(
            <Option value={data}>{data}</Option>
           ))}

           </Select>
           <span>SORT ORDER</span>
           <Select placeholder='select sort order' onChange={(value)=>{setSortorder(value)}}>
            <Option value='newtoold'>New to Old</Option>
            <Option value='oldtonew'>Old to New</Option>
           </Select>
            
        </div>
        <button onClick={()=>{window.print()}} style={{marginLeft:'5px',borderRadius:"10px",cursor:'pointer' }}>PRINT</button>
        <Table columns={columns} dataSource={displaydata} pagination={false} />

    </Layout2>
  )
}

export default Teachersumnon
