import React,{useEffect}from "react";
import { Form, Input, message } from "antd";
import "../styles/Registerstyles.css";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import { useDispatch,useSelector } from "react-redux";
//import { useDispatch, useSelector } from "react-redux";
import { showloading, hideloading } from "../redux/features/alertslice";
import {setRole} from "../redux/features/roleslice";
import { red } from "colors";

const Login = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  
  const onFinishHandler = async (values) => {
    //console.log(values);
    try {
      dispatch(showloading());
      const res = await axios.post("/api/v1/admin/login", values);
      // window.location.reload();
      dispatch(hideloading());
      if (res.data.success) {
        localStorage.setItem("token", res.data.token);
        message.success(res.data.message);
        const userRole=res.data.admin.role;
        dispatch(setRole(userRole));
console.log(userRole);
navigate("/");
        
      
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      dispatch(hideloading());
      console.log(error);
      message.error("something went wrong");
    }
  };
 
   
  return (
    <>
      <div className="form-container">
        <Form
          layout="vertical"
          onFinish={onFinishHandler}
          className="register-form"
        >
          <h3 className="text-center">LOGIN</h3>

          <Form.Item label="Email" name="email">
            <Input type="email" required />
          </Form.Item>
          <Form.Item label="Password" name="password">
            <Input type="password" required />
          </Form.Item>
          <Link to="/register" className="m-2">
            Not A User Register Here
          </Link>
          <button type="submit" className="btn btn-primary">
            Login
          </button>
          <div style={{color:"#B2BEB5"}}>
            To see Admin Features,use: 
            <p style={{margin:'0px'}}>id:admin@gmail.com </p> 
            <p style={{margin:'0px'}}> password:admin </p> 
          </div>
        </Form>
      </div>
    </>
  );
};

export default Login;
