import React, { useEffect, useState } from "react";
import Layout from "../components/Layout";
import { message,Switch,Table } from "antd";
import axios from "axios";
import { useDispatch } from "react-redux";
import { setRole } from "../redux/features/roleslice";

const Userlist = () => {
  const [userlist, setUserlist] = useState([]);
  const dispatch=useDispatch();
  dispatch(setRole("admin"));
  const getusers = async () => {
    try {
      const res = await axios.get("/api/v1/admin/getalluser", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
          'Cache-Control':'no-store',
            Pragma: 'no-cache',
      Expires: '0'
        },
      });
      if (res.data.success) {
        message.success(res.data.message);
        //dispatch(setRole("admin"));
        setUserlist(res.data.data);
      } else {
        
        message.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      message.error("someting went wrong");
    }
  };

  useEffect(()=>{
getusers();
  },[]);

  const updateuserrole=async(userid,newrole)=>{
try{
const res=await axios.post("/api/v1/admin/updaterole",{
    id:userid,
    role:newrole
},{
    headers:{
        Authorization:`Bearer ${localStorage.getItem("token")}`
    }
});
console.log(res);
if(res.data.success){
    message.success(res.data.message);

    window.location.reload();
}else{
    message.error(res.data.message);
}
}catch(error){
    console.log(error);
    message.error("something went wrong");
}
  };

const column=[
    {
        title:"User Name",
        dataIndex:"name",
        key:"name"
    },
    {
        title:"Email",
        dataIndex:"email",
        key:"email"
    },
    {
        title:"Role",
        dataIndex:"role",
        key:"role"
    },
    {
        title:"Actions",
       key:"actions",
       render:(_,record)=>(
        <Switch
        checked={record.role==="editor"}
        checkedChildren="editor"
        unCheckedChildren="viewer"
        onClick={()=>{updateuserrole(record._id,record.role==="editor"?"viewer":"editor")}}
        />
       )
    }
];

  return (<Layout>
<div style={{alignItems:'center',justifyContent:'center',display:'flex',fontSize:'34px',fontFamily:'bold'}}><p>USERS</p></div>
<Table columns={column} dataSource={userlist} pagination={false}/>



  </Layout>);
};

export default Userlist;
