import React, { useEffect, useState,useRef } from "react";
import Layout from "../components/Layout";
import { Form, Input, Row, Col, message, Select, DatePicker } from "antd";

import "../styles/Newtechpagestyles.css";

import { useParams, useNavigate } from "react-router-dom";
import axios from "axios";
import moment from "moment";
import { useDispatch, useSelector } from "react-redux";
import { setRole } from "../redux/features/roleslice";
const { Option } = Select;

const Purchaselist = () => {
     const roles = useSelector((State) => State.role.role);
      const dispatch = useDispatch();
const [purchasedata,setPurchasedata]=useState([]);
const[edititem,setEdititem]=useState(null);
const[deletecount,setDeletecount]=useState(0);
 

        useEffect(() => {
          if (roles === "admin" || roles === "editor") {
            localStorage.setItem("role", roles);
          }
        }, [roles]);
      
        useEffect(() => {
          const savedRole = localStorage.getItem("role");
          if (savedRole && savedRole !== roles) {
            dispatch(setRole(savedRole)); // Sync Redux with localStorage
          }
        }, [dispatch]);

        const getallpurchasedata = async () => {
            try {
              const res = await axios.get("/api/v1/tech/lab/getallpurchasedata ", {
                headers: {
                  Authorization: `Bearer ${localStorage.getItem("token")}`,
                },
              });
              if (res.data.success) {
                message.success(res.data.message);
                setPurchasedata(res.data.data);
              } else {
                message.error(res.data.messgae);
              }
            } catch (error) {
              console.log(error);
              message.error("something went wrong");
            }
          };
          useEffect(() => {
            getallpurchasedata();
          }, []);

          const handledelete=async(item)=>{
            try{
                const res=await axios.post("/api/v1/tech/lab/deletepurchase",{
                    item
                },{
                    headers: {
                        Authorization: `Bearer ${localStorage.getItem("token")}`,
                      },
                });

                if (res.data.success) {
                    message.success(res.data.message);
                     window.location.reload();
                  } else {
                    message.error(res.data.messgae);
                  }

            }catch(error){
                console.log(error);
              message.error("something went wrong");
            }
          };

          const handlepartialdelete=async(item)=>{
            try{
                const res=await axios.post("/api/v1/tech/lab/deletepartial",{
                    item:item,
                    count:deletecount
                },{
                    headers: {
                        Authorization: `Bearer ${localStorage.getItem("token")}`,
                      },
                });

                if (res.data.success) {
                    message.success(res.data.message);
                    let data=purchasedata.map((item)=>
                      item._id===edititem?{...item,count:item.count-deletecount}:item
                    ).filter(item=>item.count>0);
                    setPurchasedata(data);
                    setDeletecount(0);
                    setEdititem(null);
                    // window.location.reload();
                  } else {
                    message.error(res.data.messgae);
                  }

            }catch(error){
                console.log(error);
              message.error("choose loweer value");
              window.location.reload();
            }
          };


          const deletelist=async()=>{
            try{
              const res=await axios.post("/api/v1/tech/lab/deletelist",{},
              {
                  headers: {
                      Authorization: `Bearer ${localStorage.getItem("token")}`,
                    },
              });

              if (res.data.success) {
                  message.success(res.data.message);
                   window.location.reload();
                } else {
                  message.error(res.data.messgae);
                }

          }catch(error){
              console.log(error);
            message.error("something went wrong");
          }
          };
 
  const changevalue=async()=>{
    setDeletecount(0);
    setEdititem(null);
    }

  return (
    <Layout>
        <div style={{alignItems:'center',justifyContent:'center',display:'flex',fontSize:'34px',fontFamily:'bold'}}><p>PURCHASE SUMMARY NON TECHNICAL </p></div>
       <div style={{display:"flex",justifyContent:"space-between",alignItems:"center",}}>

       <button
          onClick={() => window.print()}
          style={{
            borderRadius: "10px",
            cursor: "pointer",
            color: "white",
            backgroundColor: "blue",
            padding: " 6px 10px",
            border: "none",
          }}
        >
          PRINT
        </button>

        <span style={{fontSize:"20px"}}> Date:{new Date().toLocaleDateString()}</span>

        <button
          onClick={()=>{if(window.confirm("are you sure to delete entire list")){deletelist()}}}
          style={{
            borderRadius: "10px",
            cursor: "pointer",
            color: "#FFFFFF",
            backgroundColor: "#FF6347",
            padding: " 6px 10px",
            border: "none",
          }}
        >
        DELETE LIST
        </button>

      
       </div>
       

       <div>
 
      <table border="1"
       className="table"
       style={{
         width: "100%",
         borderCollapse: "collapse",
         marginTop: "10px",
       }}>
        <thead>
          <tr>
            <th>ITEM</th>
            <th> QUANTITY</th>
            <th>ACTION</th> {/* New column for delete button */}
          </tr>
        </thead>
        <tbody >
           
            {purchasedata.length>0 ? purchasedata.map((item) => (
              <tr key={item._id}>
                <td>{item._id}</td>
                <td>{item.count}</td>
                <td >
                  <button
                    onClick={()=>{
                        if(window.confirm("are you sure")){
                            handledelete(item._id)}
                        }
                      
                    }
                    style={{
                      background: "none",
                      border: "none",
                      cursor: "pointer",
                      fontSize: "18px",
                    }}
                  >
                    
                  </button>

                  <button
                    onClick={() => setEdititem(item._id)}
                    style={{ background: "none", border: "none", cursor: "pointer", fontSize: "18px", marginLeft: "10px" }}
                  >
                    
                  </button>
                  {edititem===item._id && (
                    <div style={{display:"flex",alignItems:"center",gap:"10px" }}  >
                      <input type="number" min={1} max={item.count} value={deletecount} onChange={(e)=>setDeletecount(Number(e.target.value))}/>
                      <button onClick={()=>{handlepartialdelete(item._id)}} style={{border:"none",borderRadius:"5px",color:"white",backgroundColor:"green",cursor:"pointer",padding:"5px 6px"}}>REDUCE</button> 
                      <button onClick={changevalue}  style={{border:"none",borderRadius:"5px",color:"#D3D3D3",backgroundColor:"#F8F8FF",cursor:"pointer",padding:"5px 6px"}}>HIDE</button> 
                    </div>
                  
                  )}
                </td>
              </tr>
            
          )
          ):( 
            <tr>
                <td colSpan={3} style={{textAlign:"center",fontWeight:"bold"}}>No Data Available</td>
            </tr>
          )}
        </tbody>
      </table>
    </div>
    </Layout>
  )
}

export default Purchaselist
