import React, { useEffect, useState } from "react";
import { Form, Input, Row, Col, message } from "antd";
import axios from "axios";
import Layout from "./../../components/Layout";
import { useParams, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { setRole } from "../../redux/features/roleslice";

const Labedit = () => {
  const params = useParams();
  const navigate = useNavigate();
  const [labinfo, setLabinfo] = useState(null);

  const roles = useSelector((State) => State.role.role);
  const dispatch = useDispatch();
  
    useEffect(() => {
      if (roles === "admin" || roles === "editor") {
        localStorage.setItem("role", roles);
      }
    }, [roles]);
  
    useEffect(() => {
      const savedRole = localStorage.getItem("role");
      if (savedRole && savedRole !== roles) {
        dispatch(setRole(savedRole)); // Sync Redux with localStorage
      }
    }, [dispatch]);
  const getlabdata = async () => {
    const res = await axios.post(
      "/api/v1/tech/lab/getlabdata",
      {
        labid: params.labid,
      },
      {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      }
    );
    if (res.data.success) {
      setLabinfo(res.data.data);
      //   Form.setFieldsValue(res.data.data);
      //console.log(labdata);
      message.success(res.data.message);
    } else {
      message.error(res.data.message);
    }
  };

  useEffect(() => {
    // eslint-disable-next-line react-hooks/exhaustive-deps
    getlabdata();
  }, []);

  const handleform = async (values) => {
    values.labid = params.labid;
    try {
      const res = await axios.post("/api/v1/tech/lab/editlab", values, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });

      if (res.data.success) {
        message.success(res.data.message);
        navigate("/lab");
      } else {
        message.error(res.data.message);
      }
      window.location.reload();
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  return (
    <>
      <Layout>
        {labinfo && (
          <div className="form-div">
            <Form
              layout="vertical"
              className="m-2"
              onFinish={handleform}
              initialValues={labinfo}
            >
              <h3 className="text-center">Edit Lab</h3>
              <Row gutter={20}>
                <Col xs={24} md={24} lg={5}>
                  <Form.Item
                    label="Lab name"
                    name="labname"
                    rules={[{ required: true }]}
                  >
                    <Input type="text" placeholder="type lab name" />
                  </Form.Item>
                </Col>
                <Col xs={24} md={24} lg={5}>
                  <Form.Item
                    label="Lab number"
                    name="labno"
                    rules={[{ required: true }]}
                  >
                    <Input type="number" placeholder="type lab number" />
                  </Form.Item>
                </Col>
                <Col xs={24} md={24} lg={5}>
                  <Form.Item
                    label="Building name"
                    name="buildingname"
                    rules={[{ required: true }]}
                  >
                    <Input type="text" placeholder="type building name" />
                  </Form.Item>
                </Col>
                <Col xs={24} md={24} lg={5}>
                  <Form.Item
                    label="Floor number"
                    name="floorno"
                    rules={[{ required: true }]}
                  >
                    <Input type="number" placeholder="type floor number" />
                  </Form.Item>
                </Col>
                <Col xs={24} md={24} lg={4}>
                  <button
                    className="btn btn-primary"
                    style={{ width: "180px", marginTop: "25px" }}
                    type="submit"
                  >
                    Submit
                  </button>
                </Col>
              </Row>
            </Form>
          </div>
        )}
      </Layout>
    </>
  );
};

export default Labedit;
