import React, { useEffect, useState } from "react";
import { Form, Input, Row, Col, message } from "antd";
import Layout from "../../components/Layout";
import { useParams, useNavigate } from "react-router-dom";
import axios from "axios";
import { useDispatch, useSelector } from "react-redux";
import { setRole } from "../../redux/features/roleslice";

const Classedit = () => {
  const navigate = useNavigate();
  const params = useParams();
  const [classdata, setClassdata] = useState(null);

  const roles = useSelector((State) => State.role.role);
  const dispatch = useDispatch();
  
   useEffect(() => {
      if (roles === "admin" || roles === "editor") {
        localStorage.setItem("role", roles);
      }
    }, [roles]);
  
    useEffect(() => {
      const savedRole = localStorage.getItem("role");
      if (savedRole && savedRole !== roles) {
        dispatch(setRole(savedRole)); // Sync Redux with localStorage
      }
    }, [dispatch]);
  const handleform = async (values) => {
    values.classid = params.classid;
    try {
      const res = await axios.post("/api/v1/tech/editclass", values, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });

      if (res.data.success) {
        message.success(res.data.message);
        navigate("/class");
      } else {
        message.error(res.data.message);
      }
      window.location.reload();
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  const getclassdata = async () => {
    const res = await axios.post(
      "/api/v1/tech/getclass",
      {
        classid: params.classid,
      },
      {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      }
    );
    if (res.data.success) {
      setClassdata(res.data.data);
      console.log(classdata);
      message.success(res.data.message);
    } else {
      message.error(res.data.message);
    }
  };

  useEffect(() => {
    // eslint-disable-next-line react-hooks/exhaustive-deps
    getclassdata();
  }, []);

  return (
    <>
      <Layout>
        {classdata && (
          <div className="form-div">
            <Form
              layout="vertical"
              onFinish={handleform}
              className="m-2"
              initialValues={classdata}
            >
              <h3 className="text-center">Edit Class</h3>
              <Row gutter={20}>
                <Col xs={24} md={24} lg={8}>
                  <Form.Item
                    label="Class name"
                    name="classname"
                    rules={[{ required: true }]}
                  >
                    <Input placeholder="type class name" />
                  </Form.Item>
                </Col>
                <Col xs={24} md={24} lg={8}>
                  <Form.Item
                    label="Class number"
                    name="classno"
                    rules={[{ required: true }]}
                  >
                    <Input type="number" placeholder="type class number" />
                  </Form.Item>
                </Col>
                <Col xs={24} md={24} lg={8}>
                  <Form.Item
                    label="Building name"
                    name="buildingname"
                    rules={[{ required: true }]}
                  >
                    <Input type="text" placeholder="type building name" />
                  </Form.Item>
                </Col>
                <Col xs={24} md={24} lg={8}>
                  <Form.Item
                    label="Floor number"
                    name="floorno"
                    rules={[{ required: true }]}
                  >
                    <Input type="number" placeholder="type floor number" />
                  </Form.Item>
                </Col>
                <Col xs={24} md={24} lg={8}>
                  <button
                    className="btn btn-primary"
                    style={{ width: "200px", marginTop: "20px" }}
                    type="submit"
                  >
                    Submit
                  </button>
                </Col>
              </Row>
            </Form>
          </div>
        )}
      </Layout>
    </>
  );
};

export default Classedit;
