import React, { useEffect, useState } from "react";
import Layout from "../../components/Layout";
import { Form, Input, Row, Col, message, Select, DatePicker } from "antd";

import "../../styles/Newtechpagestyles.css";

import { useParams, useNavigate } from "react-router-dom";
import axios from "axios";
import moment from "moment";
import { useDispatch, useSelector } from "react-redux";
import { setRole } from "../../redux/features/roleslice";


const { Option } = Select;

const Teacherdetail = () => {
  const [teacherdetails, setTeacherdetails] = useState([]);
  const [itemtype, setItemtype] = useState();
  const [formattedDates, setFormattedDates] = useState({});
  const params = useParams();
   const [teachername,setTeachername]=useState();
    const [teachers,setTeachers]=useState([]);
  const navigate = useNavigate();

  const roles = useSelector((State) => State.role.role);
  const dispatch = useDispatch();
  
  useEffect(() => {
    if (roles === "admin" || roles === "editor") {
      localStorage.setItem("role", roles);
    }
  }, [roles]);

  useEffect(() => {
    const savedRole = localStorage.getItem("role");
    if (savedRole && savedRole !== roles) {
      dispatch(setRole(savedRole)); // Sync Redux with localStorage
    }
  }, [dispatch]);

  const getalllabs = async () => {
    try {
      const res = await axios.get("/api/v1/tech/teacher/getallteacher", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });
      if (res.data.success) {
        message.success(res.data.message);
        setTeachers(res.data.data);
      } else {
        message.error(res.data.messgae);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  useEffect(()=>{
    const lab=teachers.find((data)=>data._id===params.teacherid);
     if(lab){
      setTeachername(lab.teachername);
     }
      },[teachers,params.teacherid]);

   useEffect(() => {
      getalllabs();
    }, []);
  
  const handlefinish = async (values) => {
    // console.log("Received values:", values);
    values.teacherid = params.teacherid;
    if (values.itemtype === "other" && values.otheritemtype) {
      values.itemtype = values.otheritemtype;
      delete values.otheritemtype;
    }
    try {
      const res = await axios.post(
        "/api/v1/tech/teacher/addteacherdetail",
        values
      );
      if (res.data.success) {
        message.success(res.data.message);
      } else {
        message.error(res.data.message);
      }
      window.location.reload();
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };
  /*
  const processdates = async () => {
    const dates = {};
    const details = teacherdetails;
    for (let data of details) {
      dates[data._id] = await freshdate(data.installationdate);
    }
    setFormattedDates(dates);
  };
*/
  const getdetails = async () => {
    try {
      const res = await axios.post(
        "/api/v1/tech/teacher/getdetailofteacher",
        {
          teacherid: params.teacherid,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      if (res.data.success) {
        message.success(res.data.message);
        setTeacherdetails(res.data.data);
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };
  /*
  const freshdate = async (isodate) => {
    const date = moment(isodate);
    const day = date.date();
    let suffix = "";
    const j = day % 10;
    const k = day % 100;

    if (j === 1 && k !== 11) {
      suffix = "st";
    } else if (j === 2 && k !== 12) {
      suffix = "nd";
    } else if (j === 3 && k !== 13) {
      suffix = "rd";
    } else {
      suffix = "th";
    }

    const freshdate = `${day}${suffix} ${date.format("MMMM-YYYY")} `;
    return freshdate;
  };

   
*/
  useEffect(() => {
    getdetails();
  }, []);
  /*
  useEffect(() => {
    processdates();
  }, [teacherdetails]);
*/
  const handledelete = async (id) => {
    try {
      const res = await axios.post(
        "/api/v1/tech/teacher/deleteteacherdetail",
        {
          id,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      if (res.data.success) {
        message.success(res.data.message);
        window.location.reload();
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  return (
    <>
      <Layout>
        <div style={{alignItems:'center',justifyContent:'center',display:'flex',fontSize:'34px',fontFamily:'bold'}}><p>{teachername}</p></div>
                <div
                  className="form-div"
                  style={{
                    border: "1px solid black",
                    width: "100%",
                    float: "left",
                    margin: "2px",
                  }}
                >
                  <Form layout="vertical" onFinish={handlefinish}>
                    <h5 className="text-center">Add Item</h5>
                    <Row gutter={20}>
                      <Col xs={24} md={24} lg={5}>
                        <Form.Item
                          name="itemtype"
                          label="Item Type"
                          rules={[{ required: true, message: "Item type is required" }]}
                        >
                          <Select
                            placeholder="select item type"
                            onChange={(value) => setItemtype(value)}
                          >
                    <Option value="speaker">Speaker</Option>
                    <Option value="microphone">Microphone</Option>
                    <Option value="desktop">Desktop Computer</Option>
                    <Option value="laptop">Laptop</Option>
                    <Option value="projector">Projector</Option>
                    <Option value="interactive-whiteboard">
                      Interactive Whiteboard
                    </Option>
                    <Option value="printer">Printer</Option>
                    <Option value="scanner">Scanner</Option>
                    <Option value="router">Router</Option>
                    <Option value="switch">Network Switch</Option>
                    <Option value="server">Server</Option>
                    <Option value="tablet">Tablet</Option>
                    <Option value="smartphone">Smartphone</Option>
                    <Option value="external-hard-drive">
                      External Hard Drive
                    </Option>
                    <Option value="usb-device">USB Device</Option>
                    <Option value="camera">Camera</Option>
                    <Option value="document-camera">Document Camera</Option>
                    <Option value="monitor">Monitor</Option>
                    <Option value="headphones">Headphones</Option>
                    <Option value="webcam">Webcam</Option>
                    <Option value="vr-headset">VR Headset</Option>
                    <Option value="coding-kits">Coding Kits</Option>
                    <Option value="drone">Drone</Option>
                    <Option value="3d-printer">3D Printer</Option>
                    <Option value="arduino-kits">Arduino Kits</Option>
                    <Option value="raspberry-pi">Raspberry Pi</Option>
                    <Option value="network-tools">Network Tools</Option>
                    <Option value="other">Other</Option>
                  </Select>
                </Form.Item>
              </Col>
              
                    {itemtype === "other" && (
                                   <Col xs={24} md={24}  lg={5}>
                                     <Form.Item
                                       label="other item name"
                                       name="otheritemtype"
                                       rules={[
                                         { required: true, message: "other item name required" },
                                       ]}
                                     >
                                       <Input type="text" placeholder="type item name" />
                                     </Form.Item>
                                   </Col>
                                 )}
                                 <Col xs={24} md={24}  lg={5}>
                                   <Form.Item
                                     name="installationdate"
                                     label="Installation Date"
                                     rules={[
                                       {
                                         required: true,
                                         message: "Installation date is required",
                                       },
                                     ]} 
                                   >
                                     <DatePicker style={{ width: "100%" }} />
                                   </Form.Item>
                                 </Col>
                                 <Col xs={24} md={24}  lg={5}>
                                   <Form.Item
                                     name="description"
                                     label="Description"
                                     rules={[
                                       { required: true, message: "Description is required" },
                                     ]}
                                   >
                                     <Input.TextArea
                                       placeholder="enter description"
                                       rows={1}
                                       style={{ width: "100%" }}
                                     />
                                   </Form.Item>
                                 </Col>
                   
                                 {itemtype === "desktop" && (
                                   <div style={{ width: "100%" }}>
                                     <Row gutter={20}>
                                     <Col xs={24} md={24}  lg={5}>
                                       <Form.Item
                                         name="desktopnumber"
                                         label="Desktop Number"
                                         rules={[
                                           {
                                             required: true,
                                             message: "Desktop number is required",
                                           },
                                         ]}
                                       >
                                         <Input
                                           type="text"
                                           style={{ width: "100%" }}
                                           placeholder="enter desktop number"
                                         />
                                       </Form.Item>
                                     </Col>
                                     <Col xs={24} md={24}  lg={5}>
                                       <Form.Item
                                         name="ipaddress"
                                         label="IP Address"
                                         rules={[
                                           {
                                             required: true,
                                             message: "Ip address is required",
                                           },
                                         ]}
                                       >
                                         <Input
                                           type="text"
                                           style={{ width: "100%" }}
                                           placeholder="enter ip address"
                                         />
                                       </Form.Item>
                                     </Col>
                                     <Col xs={24} md={24}  lg={5}>
                                       <Form.Item
                                         name="monitornumber"
                                         label="Monitor Number"
                                         rules={[
                                           {
                                             required: true,
                                             message: "Monitor number is required",
                                           },
                                         ]}
                                       >
                                         <Input type="text" placeholder="enter monitor number" />
                                       </Form.Item>
                                     </Col>
                                     <Col xs={24} md={24}  lg={5}>
                                       <Form.Item
                                         name="monitorname"
                                         label="Monitor Attributes"
                                         rules={[{ required: true }]}
                                       >
                                         <Input type="text" placeholder="Brand, Model, Size" />
                                       </Form.Item>
                                     </Col>
                                     <Col xs={24} md={24}  lg={5}>
                                       <Form.Item
                                         name="mousenumber"
                                         label="Mouse Number"
                                         rules={[
                                           { required: true, message: "Mouse number is required" },
                                         ]}
                                       >
                                         <Input type="text" placeholder="enter mouse number" />
                                       </Form.Item>
                                     </Col>
                                     <Col xs={24} md={24}  lg={5}>
                                       <Form.Item
                                         name="mousename"
                                         label="Mouse Attributes"
                                         rules={[{ required: true }]}
                                       >
                                         <Input type="text" placeholder="Brand and Model" />
                                       </Form.Item>
                                     </Col>
                                     <Col xs={24} md={24}  lg={5}>
                                       <Form.Item
                                         name="keyboardnumber"
                                         label="Keyboard Number"
                                         rules={[
                                           { required: true, message: "keyboard no required" },
                                         ]}
                                       >
                                         <Input type="text" placeholder="enter keyboard number" />
                                       </Form.Item>
                                     </Col>
                                     <Col xs={24} md={24}  lg={5}>
                                       <Form.Item
                                         name="keyboardname"
                                         label="Keyboard Attributes"
                                         rules={[{ required: true }]}
                                       >
                                         <Input type="text" placeholder="Brand and model" />
                                       </Form.Item>
                                     </Col>
                                     <Col xs={24} md={24}  lg={5}>
                                       <Form.Item
                                         name="cpunumber"
                                         label="CPU Number"
                                         rules={[
                                           { required: true, message: "CPU number is required" },
                                         ]}
                                       >
                                         <Input type="text" placeholder="enter cpu number" />
                                       </Form.Item>
                                     </Col>
                                     <Col xs={24} md={24}  lg={5}>
                                       <Form.Item
                                         name="cpuname"
                                         label="CPU Attributes"
                                         rules={[{ required: true }]}
                                       >
                                         <Input type="text" placeholder="Brand, Model, Speed" />
                                       </Form.Item>
                                     </Col>
                                     </Row>
                                   </div>
                                 )}
                   
                                 <Col xs={24} md={24}  lg={5}>
                                   <Form.Item>
                                     <button
                                       type="submit"
                                       className="btn btn-primary"
                                       style={{ width: "100%", marginLeft: "0px",marginTop:'30px' }}
                                     >
                                       Submit
                                     </button>
                                   </Form.Item>
                                 </Col>
                               </Row>
                             </Form>
                           </div>
                   
        

        
        <table
          className="table"
          style={{
            width: "100%",
            borderCollapse: "collapse",
            marginTop: "10px",
          }}
        >
          <thead>
            <tr>
              <th
                style={{
                  border: "2px solid black",
                  textAlign: "center",
                  padding: "10px",
                  color: "red",
                }}
              >
                Item Type
              </th>
              <th
                style={{
                  border: "2px solid black",
                  textAlign: "center",
                  padding: "10px",
                  color: "red",
                }}
              >
                Installation Date
              </th>
              <th
                style={{
                  border: "2px solid black",
                  textAlign: "center",
                  padding: "10px",
                  color: "red",
                }}
              >
                Description
              </th>
              <th
                style={{
                  border: "2px solid black",
                  textAlign: "center",
                  padding: "10px",
                  color: "red",
                }}
              >
                Desktop Info
              </th>
              <th
                style={{
                  border: "2px solid black",
                  textAlign: "center",
                  padding: "10px",
                  color: "red",
                }}
              >
                Actions
              </th>
            </tr>
          </thead>
          <tbody>
            {teacherdetails &&
              teacherdetails.map((data) => (
                <tr key={data._id}>
                  <td
                    style={{
                      border: "2px solid black",
                      textAlign: "center",
                      padding: "10px",
                    }}
                  >
                    <b>{data.itemtype}</b>
                  </td>
                  <td
                    style={{
                      border: "2px solid black",
                      textAlign: "center",
                      padding: "10px",
                    }}
                  >
                    {new Date(data.installationdate).toLocaleDateString()}
                  </td>
                  <td
                    style={{
                      border: "2px solid black",
                      textAlign: "center",

                      width: "350px",
                    }}
                  >
                    <div style={{ maxHeight: "150px", overflowY: "auto" }}>
                      {data.description || "N/A"}
                    </div>
                  </td>
                  <td
                    style={{
                      border: "2px solid black",
                      textAlign: "center",
                    }}
                  >
                    {data.itemtype === "desktop" && (
                      <div
                        className="desktop-info"
                        style={{
                          maxHeight: "150px",
                          overflowY: "auto",
                        }}
                      >
                        <p>
                          <b>Desktop Number:</b> {data.desktopnumber}
                        </p>
                        <p>
                          <b>IP Address:</b> {data.ipaddress}
                        </p>
                        <p>
                          <b>Monitor Number:</b> {data.monitornumber}
                        </p>
                        <p>
                          <b>Monitor Name:</b> {data.monitorname}
                        </p>
                        <p>
                          <b>Mouse Number:</b> {data.mousenumber}
                        </p>
                        <p>
                          <b>Mouse Name:</b> {data.mousename}
                        </p>
                        <p>
                          <b>Keyboard Number:</b> {data.keyboardnumber}
                        </p>
                        <p>
                          <b>Keyboard Name:</b> {data.keyboardname}
                        </p>
                        <p>
                          <b>CPU Number:</b> {data.cpunumber}
                        </p>
                        <p>
                          <b>CPU Name:</b> {data.cpuname}
                        </p>
                      </div>
                    )}
                  </td>
                  <td
                    style={{
                      border: "2px solid black",
                      textAlign: "center",
                      padding: "10px",
                    }}
                  >
                    <i
                      className="fa-solid fa-trash icon"
                      style={{ cursor: "pointer", margin: "10px" }}
                      onClick={() => {
                        if (window.confirm("Do you want to delete?")) {
                          handledelete(data._id);
                        }
                      }}
                    ></i>
                    <i
                      className="fa-solid fa-pen-to-square edit"
                      style={{ cursor: "pointer", margin: "10px" }}
                      onClick={() => {
                        if (window.confirm("Do you want to edit?")) {
                          navigate(`/teacherdetailedit/${data._id}`);
                        }
                      }}
                    ></i>
                  </td>
                </tr>
              ))}
          </tbody>
        </table>
      </Layout>
    </>
  );
};

export default Teacherdetail;
