import React from 'react'
import { useNavigate } from "react-router-dom";
import "../../styles/Techpagestyles.css";
import Layout2 from "../../components/Layout2";

const data = [
    {
      name: "Teacher",
      path: "/teachersum",
      icon: "fa-solid fa-chalkboard-user",
    },
    {
      name: "Class",
      path: "/classsum",
      icon: "fa-solid fa-house",
    },
    {
      name: "Lab",
      path: "/labsum",
      icon: "fa-solid fa-computer",
    },
    {
      name: "Server",
      path: "/serversum",
      icon: "fa-solid fa-server",
    }
     
  ];

const Techsum = () => {
    const navigate = useNavigate();
  return (
    <>
    <Layout2> 
    <div style={{alignItems:'center',justifyContent:'center',display:'flex',fontSize:'24px',fontFamily:'bold'}}><p>TECHNICAL SUMMARY</p></div>
       <div style={{marginTop:"8rem"}}> 
      <section className="container1">
          {data.map((data) => {
            return (
              <div
                className="box1"
                onClick={() => {
                  navigate(data.path);
                }}
              >
                <i class={data.icon}></i>

                <b>{data.name}</b>
              </div>
            );
          })}
        </section>
        </div>
        </Layout2>
    </>
  )
}

export default Techsum
