import React, { useEffect, useState } from "react";
import Layout from "../../components/Layout";
import { Form, Input, Row, Col, message } from "antd";
import axios from "axios";
 import "../../styles/Techpagestyles.css";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { setRole } from "../../redux/features/roleslice";

const Teacherpage = () => {
  const navigate=useNavigate();
  const [teacherlist,setTeacherlist]=useState([]);

  const roles = useSelector((State) => State.role.role);
  const dispatch = useDispatch();
  
  useEffect(() => {
    if (roles === "admin" || roles === "editor") {
      localStorage.setItem("role", roles);
    }
  }, [roles]);

  useEffect(() => {
    const savedRole = localStorage.getItem("role");
    if (savedRole && savedRole !== roles) {
      dispatch(setRole(savedRole)); // Sync Redux with localStorage
    }
  }, [dispatch]);
  
    const handleform = async (values) => {
        try {
          const res = await axios.post("/api/v1/tech/teacher/addteacher", values);
    
          if (res.data.success) {
            message.success(res.data.message);
            window.location.reload();
          } else {
            message.error(res.data.message);
             window.location.reload();
          }
        } catch (error) {
          console.log(error);
          message.error("something went wrong");
        }
      };


      const getallteacher = async () => {
        const res = await axios.get("/api/v1/tech/teacher/getallteacher", {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });
    
        if (res.data.success) {
          setTeacherlist(res.data.data);
          message.success("all teachers get");
        } else {
          message.error(res.data.message);
        }
      };
    
      useEffect(() => {
        getallteacher();
      }, []);

      const handledelete = async (id) => {
        try {
          // eslint-disable-next-line no-restricted-globals
          var result = confirm("really want to delete");
          if (result) {
            const res = await axios.post(
              "/api/v1/tech/teacher/deleteteacher",
              { id },
              {
                headers: {
                  Authorization: `Bearer ${localStorage.getItem("token")}`,
                },
              }
            );
            if (res.data.success) {
              message.success(res.data.message);
            } else {
              message.error(res.data.message);
            }
            window.location.reload();
          }
        } catch (error) {
          console.log(error);
          message.error("something went wrong");
        }
      };

  return (
    <>
    <Layout>
    <div className="">
          <Form layout="vertical" onFinish={handleform} className="m-2">
            <h3 className="text-center">Teacher's Room</h3>
            <Row gutter={20}>
              <Col xs={24} md={24} lg={5}>
                <Form.Item
                  label="Teacher name"
                  name="teachername"
                  rules={[{ required: true }]}
                >
                  <Input type="text" placeholder="type teacher's name" />
                </Form.Item>
              </Col>
              <Col xs={24} md={24} lg={5}>
                <Form.Item
                  label="Room number"
                  name="roomno"
                  rules={[{ required: true }]}
                >
                  <Input type="number" placeholder="type room number" />
                </Form.Item>
              </Col>
              <Col xs={24} md={24} lg={5}>
                <Form.Item
                  label="Building name"
                  name="buildingname"
                  rules={[{ required: true }]}
                >
                  <Input type="text" placeholder="type building name" />
                </Form.Item>
              </Col>
              <Col xs={24} md={24} lg={5}>
                <Form.Item
                  label="Floor number"
                  name="floorno"
                  rules={[{ required: true }]}
                >
                  <Input type="number" placeholder="type floor number" />
                </Form.Item>
              </Col>
              <Col xs={24} md={24} lg={4}>
                <button
                  className="btn btn-primary"
                  style={{ width: "180px", marginTop: "25px" }}
                  type="submit"
                >
                  Submit
                </button>
              </Col>
            </Row>
          </Form>
        </div>

        {teacherlist &&
        teacherlist.map((data) => {
          return (
            <div className="card m-2 cardstyle">
              <div
                style={{ cursor: "pointer" }}
                className="card-header"
                onClick={() => {
                  navigate(`/teacherdetails/${data._id}`);
                }}
              >
                Teacher Name:
                <b> {data.teachername}</b>
              </div>

              <div className="card-body p-2">
                <p>
                  <b>Room No: </b>
                  {data.roomno}
                </p>
                <p>
                  <b>Building Name: </b>
                  {data.buildingname}
                </p>
                <p>
                  <b>Floor No: </b>
                  {data.floorno}
                </p>
                <p>
                  <button
                    onClick={() => {
                      // eslint-disable-next-line no-restricted-globals
                      let result = confirm("really want to edit");
                      if (result) {
                        navigate(`/teacheredit/${data._id}`);
                      }
                    }}
                    className="btn btn-primary"
                  >
                    Edit
                  </button>
                  <button
                    onClick={() => {
                      handledelete(data._id);
                    }}
                    className="btn btn-danger"
                  >
                    Delete
                  </button>
                </p>
              </div>
            </div>
          );
        })}

    </Layout>
      
    </>
  )
}

export default Teacherpage
