import React, { useEffect, useState } from "react";
import Layout2 from "../../components/Layout2";
import { message, Select, Table } from "antd";
import axios from "axios";
const { Option } = Select;

const Serversum = () => {
  const [serverrooms, setServerrooms] = useState([]);
  const [serverroomdetails, setServerroomdetails] = useState([]);

  const [serverroomnames, setServerroomnames] = useState({});
  const [totalserverrooms, setTotalserverrooms] = useState([]);
  const [serverroomnums, setServerroomnums] = useState("all");
  const [sortorder, setSortorder] = useState("oldtonew");

  const [item, setItem] = useState("all");
  const [displaydata, setDisplaydata] = useState();

  const getallserverrooms = async () => {
    try {
      const res = await axios.get("/api/v1/tech/server/getallserver", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });
      if (res.data.success) {
        message.success(res.data.message);
        setServerrooms(res.data.data);
      } else {
        message.error(res.data.messgae);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  const getserverroomdetails = async () => {
    try {
      const res = await axios.get(
        "/api/v1/tech/server/getallserverroomdetails",
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      if (res.data.success) {
        message.success(res.data.message);
        setServerroomdetails(res.data.data);
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  const serverroomnamess = async () => {
    const serverroomnames = {};

    const allserverroomdetails = serverroomdetails;
    for (let data of allserverroomdetails) {
      serverroomnames[data._id] = await serverroomname(data.serverroomid);
    }
    setServerroomnames(serverroomnames);
  };

  const serverroomname = async (serverroomid) => {
    const allserverrooms = serverrooms;
    for (let data of allserverrooms) {
      if (data._id === serverroomid) {
        return data.roomname;
      }
    }
  };

  useEffect(() => {
    getserverroomdetails();
  }, []);

  useEffect(() => {
    getallserverrooms();
  }, []);

  useEffect(() => {
    serverroomnamess();
  }, [serverroomdetails, serverrooms]);

  useEffect(() => {
    const allserverrooms = [
      ...new Set(serverrooms.map((data) => data.roomname)),
    ];
    setTotalserverrooms(allserverrooms);
  }, [serverrooms]);

  const initialprint = async () => {
    if (item === "all") {
      const tabledatas = serverroomdetails.map((data) => ({
        key: data._id,
        product:
          data.itemtype === "desktop" ? data.desktopnumber : data.itemtype,
        date: new Date(data.installationdate).toLocaleDateString(),
        description:
          data.description +
          " " +
          (data.desktopnumber ? `desktopnumber=${data.desktopnumber}` : " ") +
          " " +
          (data.ipaddress ? `ipaddress=${data.ipaddress}` : "") +
          " " +
          (data.monitornumber ? ` monitornumber=${data.monitornumber}` : "") +
          " " +
          (data.monitorname ? ` monitorname=${data.monitorname}` : "") +
          " " +
          (data.mousenumber ? ` mousenumber=${data.mousenumber}` : "") +
          " " +
          (data.mousename ? ` mousename=${data.mousename}` : "") +
          " " +
          (data.keyboardnumber
            ? ` keyboardnumber=${data.keyboardnumber}`
            : "") +
          " " +
          (data.keyboardname ? ` keyboardname=${data.keyboardname}` : "") +
          " " +
          (data.cpunumber ? ` cpunumber=${data.cpunumber}` : "") +
          " " +
          (data.cpuname ? ` cpuname=${data.cpuname}` : "") +
          "" +
          (data.serverName ? `server name : ${data.serverName}` : " ") +
          " " +
          (data.serverType ? `server type: ${data.serverType}` : " ") +
          " " +
          (data.operatingSystem
            ? `operating system : ${data.operatingSystem}`
            : " ") +
          " " +
          (data.hostname ? `hostname : ${data.hostname}` : "") +
          " " +
          (data.macAddress ? `mac address : ${data.macAddress}` : ""),
        serverroomname: serverroomnames[data._id],
      }));
      setDisplaydata(tabledatas);
    }
  };
  useEffect(() => {
    initialprint();
  }, [serverroomdetails, serverroomnames]);

  const tabledata = (item, serverroomname, order) => {
    let displaydatas;
    if (item === "other") {
      if (serverroomname === "all") {
        const excludeitem = [
          "server",
          "desktop",
          "laptop",
          "networkSwitches",
          "routers",
          "firewalls",
          "ups",
          "coolingSystems",
          "patchPanels",
          "cableManagement",
          "storageArrays",
          "kvmSwitch",
          "serverRacks",
          "securityCameras",
          "accessControl",
          "environmentalMonitoring",
          "backupSystems",
          "fireSuppression",
          "remoteManagement",
          "pdus",
          "patchCables",
          "monitoringSoftware",
        ];
        displaydatas = serverroomdetails.filter(
          (data) => !excludeitem.includes(data.itemtype)
        );
      } else {
        const excludeitem = [
          "server",
          "desktop",
          "laptop",
          "networkSwitches",
          "routers",
          "firewalls",
          "ups",
          "coolingSystems",
          "patchPanels",
          "cableManagement",
          "storageArrays",
          "kvmSwitch",
          "serverRacks",
          "securityCameras",
          "accessControl",
          "environmentalMonitoring",
          "backupSystems",
          "fireSuppression",
          "remoteManagement",
          "pdus",
          "patchCables",
          "monitoringSoftware",
        ];
        displaydatas = serverroomdetails.filter(
          (data) =>
            !excludeitem.includes(data.itemtype) &&
            serverroomnames[data._id] === serverroomname
        );
      }
    } else if (item === "all") {
      if (serverroomname === "all") {
        displaydatas = serverroomdetails;
      } else {
        displaydatas = serverroomdetails.filter(
          (data) => serverroomnames[data._id] === serverroomname
        );
      }
    } else {
      if (serverroomname === "all") {
        displaydatas = serverroomdetails.filter(
          (data) => data.itemtype === item
        );
      } else {
        displaydatas = serverroomdetails.filter(
          (data) =>
            data.itemtype === item &&
            serverroomnames[data._id] === serverroomname
        );
      }
    }

    displaydatas.sort((a, b) => {
      const dateA = new Date(a.installationdate);
      const dateB = new Date(b.installationdate);
      return order === "newtoold" ? dateB - dateA : dateA - dateB;
    });

    const tabledatas = displaydatas.map((data) => ({
      key: data._id,
      product: data.itemtype === "desktop" ? data.desktopnumber : data.itemtype,
      date: new Date(data.installationdate).toLocaleDateString(),
      description:
        data.description +
        " " +
        (data.desktopnumber ? ` desktopnumber=${data.desktopnumber}` : "") +
        " " +
        (data.ipaddress ? ` ipaddress=${data.ipaddress}` : "") +
        " " +
        (data.monitornumber ? ` monitornumber=${data.monitornumber}` : "") +
        " " +
        (data.monitorname ? ` monitorname=${data.monitorname}` : "") +
        " " +
        (data.mousenumber ? ` mousenumber=${data.mousenumber}` : "") +
        " " +
        (data.mousename ? ` mousename=${data.mousename}` : "") +
        " " +
        (data.keyboardnumber ? ` keyboardnumber=${data.keyboardnumber}` : "") +
        " " +
        (data.keyboardname ? ` keyboardname=${data.keyboardname}` : "") +
        " " +
        (data.cpunumber ? ` cpunumber=${data.cpunumber}` : "") +
        " " +
        (data.cpuname ? ` cpuname=${data.cpuname}` : "") +
        "" +
        (data.serverName ? `server name : ${data.serverName}` : " ") +
        " " +
        (data.serverType ? `server type: ${data.serverType}` : " ") +
        " " +
        (data.operatingSystem
          ? `operating system : ${data.operatingSystem}`
          : " ") +
        " " +
        (data.hostname ? `hostname : ${data.hostname}` : "") +
        " " +
        (data.macAddress ? `mac address : ${data.macAddress}` : ""),
      serverroomname: serverroomnames[data._id],
    }));
    setDisplaydata(tabledatas);
  };

  const columns =
    item === "desktop"
      ? [
          { title: "DESKTOP NUMBER", dataIndex: "product", key: "product" },
          { title: "INSTALLATION DATE", dataIndex: "date", key: "date" },
          {
            title:  "DESCRIPTION/DESKTOP INFO.",
            dataIndex: "description",
            key: "description",
          },
          {
            title: "SERVER ROOM NAME",
            dataIndex: "serverroomname",
            key: "serverroomname",
          },
        ]
      : [
          {
            title: "ITEM NAME/DESKTOP NUMBER",
            dataIndex: "product",
            key: "product",
          },
          { title: "INSTALLATION DATE", dataIndex: "date", key: "date" },
          {
            title: "DESCRIPTION",
            dataIndex: "description",
            key: "description",
          },
          {
            title: "SERVER ROOM NAME",
            dataIndex: "serverroomname",
            key: "serverroomname",
          },
        ];
  const itemchange = (value) => {
    setItem(value);
    //tabledata(item,labnums);
  };

  const serverroomnamechange = (value) => {
    setServerroomnums(value);
    // tabledata(item,labnums);
  };
  useEffect(() => {
    tabledata(item, serverroomnums, sortorder);
  }, [item, serverroomnums, sortorder]);

  return (
    <>
      <Layout2>
      <div style={{alignItems:'center',justifyContent:'center',display:'flex',fontSize:'24px',fontFamily:'bold'}}><p>SERVER SUMMARY</p></div>
        <div
          className="d-flex justify-content-center align-items-center gap-2"
          style={{ width: "100%" }}
        >
          <span>SELECT ITEM : </span>
          <Select
            placeholder="choose an item"
            onChange={itemchange}
            style={{ width: "150px" }}
          >
            {[
              "server",
              "desktop",
              "laptop",
              "networkSwitches",
              "routers",
              "firewalls",
              "ups",
              "coolingSystems",
              "patchPanels",
              "cableManagement",
              "storageArrays",
              "kvmSwitch",
              "serverRacks",
              "securityCameras",
              "accessControl",
              "environmentalMonitoring",
              "backupSystems",
              "fireSuppression",
              "remoteManagement",
              "pdus",
              "patchCables",
              "monitoringSoftware",
              "all",
              "other",
            ].map((data) => (
              <Option value={data}>{data}</Option>
            ))}
          </Select>
          <span>SELECT SERVER ROOM : </span>
          <Select
            placeholder="choose server room name"
            onChange={serverroomnamechange}
            style={{ width: "150px" }}
          >
            <Option value="all">All</Option>
            {totalserverrooms.map((data) => (
              <Option value={data}>{data}</Option>
            ))}
          </Select>
          <span>SORT ORDER</span>
          <Select
            placeholder="select sort order"
            onChange={(value) => {
              setSortorder(value);
            }}
          >
            <Option value="newtoold">New to Old</Option>
            <Option value="oldtonew">Old to New</Option>
          </Select>
        </div>
        <button
          onClick={() => {
            window.print();
          }}
          style={{ marginLeft: "5px", borderRadius: "10px", cursor: "pointer" }}
        >
          PRINT
        </button>
        <Table columns={columns} dataSource={displaydata} pagination={false} />
      </Layout2>
    </>
  );
};

export default Serversum;
