import React,{useEffect} from "react";
import { useNavigate } from "react-router-dom";
import "../styles/Homepagestyles.css";
 
import { useDispatch, useSelector } from "react-redux";
import { setRole } from "../redux/features/roleslice";


const Layout = ({ children }) => {
 
  const dispatch = useDispatch();
  const navigate = useNavigate();
    const roles=useSelector(state=>state.role.role);
    
         useEffect(() => {
            if (roles === "admin" || roles === "editor") {
              localStorage.setItem("role", roles);
            }
          }, [roles]);
        
          useEffect(() => {
            const savedRole = localStorage.getItem("role");
            if (savedRole && savedRole !== roles) {
              dispatch(setRole(savedRole)); // Sync Redux with localStorage
            }
          }, [dispatch]);

  const handlelogout = async () => {
    if (localStorage.getItem("token")) {
      localStorage.clear();
      window.location.reload();
    }
  };

  return (
    
    <>
  {roles === "admin" ? (
     <div className="logout">
      <button className="btn btn-primary" onClick={() => navigate("/")}>
        HOME
      </button>
      <button className="btn btn-primary" onClick={() => navigate("/tech")}>
        TECHNICAL
      </button>
      <button className="btn btn-primary" onClick={() => navigate("/non-tech")}>
        NON-TECHNICAL
      </button>
      <button className="btn btn-primary" onClick={() => navigate("/userlist")}>
        USER LIST
      </button>
      <button className="btn btn-danger" onClick={handlelogout}>
        LOGOUT
      </button>
      </div>
  
  ) : (
    <div className="logout">
      <button className="btn btn-primary" onClick={() => navigate("/")}>
        HOME
      </button>
      <button className="btn btn-primary" onClick={() => navigate("/tech")}>
        TECHNICAL
      </button>
      <button className="btn btn-primary" onClick={() => navigate("/non-tech")}>
        NON-TECHNICAL
      </button>
      <button className="btn btn-danger" onClick={handlelogout}>
        LOGOUT
      </button>
    </div>
  )}
  
  {children}
</>

  );
};

export default Layout;
