import React,{useEffect} from 'react'
import "../styles/Homepagestyles.css";
import { useNavigate } from "react-router-dom"; 
import Layout2 from '../components/Layout2';
import { useDispatch, useSelector } from "react-redux";
import { setRole } from '../redux/features/roleslice';

const Summary = () => {
  const navigate = useNavigate();
  const roles = useSelector((State) => State.role.role);
  const dispatch = useDispatch();

   useEffect(() => {
      if (roles === "admin" || roles === "editor") {
        localStorage.setItem("role", roles);
      }
    }, [roles]);
  
    useEffect(() => {
      const savedRole = localStorage.getItem("role");
      if (savedRole && savedRole !== roles) {
        dispatch(setRole(savedRole)); // Sync Redux with localStorage
      }
    }, [dispatch]);
  return (
    <>
    <Layout2> 
     <div class="container">
          <div class="d-flex">
            <div
              class="box"
              id="technical"
              onClick={() => {
                
                navigate("/techsum");
                 
              }}
            >
              <b>TECHNICAL SUMMARY</b>
            </div>
            <div
              class="box"
              id="non-technical"
              onClick={() => {
                navigate("/non-tech-sum");
              }}
            >
              <b>NON-TECHNICAL SUMMARY</b>
            </div>

            </div>
            </div>
     
            </Layout2>
       
    </>
  )
}

export default Summary
