import React, { useEffect, useState } from "react";
import { Form, Input, Row, Col, message } from "antd";
import axios from "axios";
import Layout from "./../../components/Layout";
import { useParams, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { setRole } from "../../redux/features/roleslice";

const Serveredit = () => {
 const params = useParams();
  const navigate = useNavigate();
  const [serverinfo, setServerinfo] = useState(null);
  const [form]=Form.useForm();

  const roles = useSelector((State) => State.role.role);
  const dispatch = useDispatch();
  
    useEffect(() => {
      if (roles === "admin" || roles === "editor") {
        localStorage.setItem("role", roles);
      }
    }, [roles]);
  
    useEffect(() => {
      const savedRole = localStorage.getItem("role");
      if (savedRole && savedRole !== roles) {
        dispatch(setRole(savedRole)); // Sync Redux with localStorage
      }
    }, [dispatch]);
  const getserverdata = async () => {
    const res = await axios.post(
      "/api/v1/tech/server/getserverdata",
      {
        serverroomid: params.serverroomid,
      },
      {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      }
    );
    if (res.data.success) {
      setServerinfo(res.data.data);
      //   Form.setFieldsValue(res.data.data);
      //console.log(labdata);
      form.setFieldsValue(res.data.data);
      message.success(res.data.message);
    } else {
      message.error(res.data.message);
    }
  };

  useEffect(() => {
    // eslint-disable-next-line react-hooks/exhaustive-deps
    getserverdata();
  }, []);

  const handleform = async (values) => {
    values.serverroomid = params.serverroomid;
    try {
      const res = await axios.post("/api/v1/tech/server/updateserver", values, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });

      if (res.data.success) {
        message.success(res.data.message);
        navigate("/server");
      } else {
        message.error(res.data.message);
      }
      window.location.reload();
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };


  return (
    <>
     <Layout>
     <div
          className="m-2"
          style={{ border: "1px solid black", width: "300px",float:"left" }}
        >
          <Form layout="vertical" onFinish={handleform} className="m-2" form={form}>
            <h3 className="text-center">Edit Details</h3>
            <Row gutter={20}>
              <Col xs={24} md={24}>
                <Form.Item
                  label="Server Room name"
                  name="roomname"
                  rules={[{ required: true }]}
                >
                  <Input type="text" placeholder="type lab name" />
                </Form.Item>
              </Col>
              <Col xs={24} md={24}>
                <Form.Item
                  label="Room number"
                  name="roomno"
                  rules={[{ required: true }]}
                >
                  <Input type="number" placeholder="type lab number" />
                </Form.Item>
              </Col>
              <Col xs={24} md={24}>
                <Form.Item
                  label="Building name"
                  name="buildingname"
                  rules={[{ required: true }]}
                >
                  <Input type="text" placeholder="type building name" />
                </Form.Item>
              </Col>
              <Col xs={24} md={24}>
                <Form.Item
                  label="Floor number"
                  name="floorno"
                  rules={[{ required: true }]}
                >
                  <Input type="number" placeholder="type floor number" />
                </Form.Item>
              </Col>
              <Col xs={24} md={24}>
                <button
                  className="btn btn-primary"
                  style={{ width: "180px", marginTop: "25px" }}
                  type="submit"
                >
                  Submit
                </button>
              </Col>
            </Row>
          </Form>
        </div>
        
    </Layout> 
    </>
  )
}

export default Serveredit
