import React,{useEffect}from "react";
import { useNavigate } from "react-router-dom";
import "../../styles/Techpagestyles.css";
import Layout from "../../components/Layout";
import { useDispatch, useSelector } from "react-redux";
import { setRole } from "../../redux/features/roleslice";

const data = [
  {
    name: "Teacher",
    path: "/teacher",
    icon: "fa-solid fa-chalkboard-user",
  },
  {
    name: "Class",
    path: "/class",
    icon: "fa-solid fa-house",
  },
  {
    name: "Lab",
    path: "/lab",
    icon: "fa-solid fa-computer",
  },
  {
    name: "Server",
    path: "/server",
    icon: "fa-solid fa-server",
  },
  {
    name: "Dump",
    path: "/dump",
    icon: "fa-solid fa-trash",
  },
];
const Techpage = () => {

  const roles = useSelector((State) => State.role.role);
  const dispatch = useDispatch();
  
  useEffect(() => {
    if (roles === "admin" || roles === "editor") {
      localStorage.setItem("role", roles);
    }
  }, [roles]);

  useEffect(() => {
    const savedRole = localStorage.getItem("role");
    if (savedRole && savedRole !== roles) {
      dispatch(setRole(savedRole)); // Sync Redux with localStorage
    }
  }, [dispatch]);
  
  const navigate = useNavigate();
  return (
    <>
      <Layout>
      <div style={{alignItems:'center',justifyContent:'center',display:'flex',fontSize:'34px',fontFamily:'bold'}}><p>TEACHNICAL</p></div>
        <section className="container1">
          {data.map((data) => {
            return (
              <div
                className="box1"
                onClick={() => {
                  navigate(data.path);
                }}
              >
                <i class={data.icon}></i>

                <b>{data.name}</b>
              </div>
            );
          })}
        </section>
     
      </Layout>
    </>
  );
};

export default Techpage;
