import React, { useEffect, useState } from "react";
import Layout from "../../components/Layout";
import { Form, Input, Row, Col, message, Select, DatePicker } from "antd";

import "../../styles/Newtechpagestyles.css";

import { useParams, useNavigate } from "react-router-dom";
import axios from "axios";
import moment from "moment";
import { useDispatch, useSelector } from "react-redux";
import { setRole } from "../../redux/features/roleslice";
const { Option } = Select;

const Teacherdetailsnon = () => {
    const roles = useSelector((State) => State.role.role);
    const dispatch = useDispatch();
  const [teacherdetails, setTeacherdetails] = useState([]);
  const [itemtype, setItemtype] = useState();
  const [formattedDates, setFormattedDates] = useState({});
  const params = useParams();
  const navigate = useNavigate();
 const [teachername,setTeachername]=useState();
   const [teacher,setTeacher]=useState([]);

useEffect(() => {
    if (roles === "admin" || roles === "editor") {
      localStorage.setItem("role", roles);
    }
  }, [roles]);

  useEffect(() => {
    const savedRole = localStorage.getItem("role");
    if (savedRole && savedRole !== roles) {
      dispatch(setRole(savedRole)); // Sync Redux with localStorage
    }
  }, [dispatch]);

   const getallteachers = async () => {
    try {
      const res = await axios.get("/api/v1/tech/teacher/getallteacher", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });
      if (res.data.success) {
        message.success(res.data.message);
        setTeacher(res.data.data);
      } else {
        message.error(res.data.messgae);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  useEffect(()=>{
    const lab=teacher.find((data)=>data._id===params.teacherid);
     if(lab){
      setTeachername(lab.teachername);
     }
      },[teacher,params.teacherid]);

   useEffect(() => {
      getallteachers();
    }, []);

  const handlefinish = async (values) => {
    // console.log("Received values:", values);
    values.teacherid = params.teacherid;
    if (values.itemtype === "other" && values.otheritemtype) {
      values.itemtype = values.otheritemtype;
      delete values.otheritemtype;
    }
    try {
      const res = await axios.post(
        "/api/v1/tech/teacher/addteacherdetailnon",
        values
      );
      if (res.data.success) {
        message.success(res.data.message);
      } else {
        message.error(res.data.message);
      }
      window.location.reload();
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  const getdetails = async () => {
    try {
      const res = await axios.post(
        "/api/v1/tech/teacher/getdetailofteachernon",
        {
          teacherid: params.teacherid,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      if (res.data.success) {
        message.success(res.data.message);
        setTeacherdetails(res.data.data);
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

 useEffect(() => {
    getdetails();
  }, []);


  const handledelete = async (id) => {
    try {
      const res = await axios.post(
        "/api/v1/tech/teacher/deleteteacherdetailnon",
        {
          id,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      if (res.data.success) {
        message.success(res.data.message);
        window.location.reload();
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  return (
    <Layout>
      <div style={{alignItems:'center',justifyContent:'center',display:'flex',fontSize:'34px',fontFamily:'bold'}}><p>{teachername}</p></div>
      <div
        className="form-divnew"
        style={{
          border: "1px solid black",
          width: "100%",
          margin: "10px",
          float: "left",
        }}
      >
        <Form layout="vertical" onFinish={handlefinish} className="m-2">
          <h3 className="text-center">ADD ITEM</h3>
          <Row gutter={20}>
            <Col xs={24} md={24} lg={5}>
              <Form.Item
                name="itemtype"
                label="Item Type"
                rules={[{ required: true, message: "Item type is required" }]}
              >
                <Select
                  placeholder="select item type"
                  onChange={(value) => setItemtype(value)}
                >
                  <Option value="whiteboard">Whiteboard</Option>
                  <Option value="marker">Marker</Option>
                  <Option value="chalk">Chalk</Option>
                  <Option value="duster">Duster</Option>
                  <Option value="notice_board">Notice Board</Option>
                  <Option value="bookshelf">Bookshelf</Option>
                  <Option value="clock">Clock</Option>
                  <Option value="chair">Chair</Option>
                  <Option value="desk">Desk</Option>
                  <Option value="pen_holder">Pen Holder</Option>
                  <Option value="paper_stack">Paper Stack</Option>
                  <Option value="file_cabinet">File Cabinet</Option>
                  <Option value="cupboard">Cupboard</Option>
                  <Option value="curtains">Curtains</Option>
                  <Option value="trash_bin">Trash Bin</Option>
                  <Option value="water_bottle">Water Bottle</Option>
                  <Option value="notepad">Notepad</Option>
                  <Option value="globe">Globe</Option>
                  <Option value="poster">Poster</Option>
                  <Option value="calendar">Calendar</Option>
                  <Option value="plant">Plant</Option>
                  <Option value="student_chair">Student Chair</Option>
                  <Option value="bulletin_board">Bulletin Board</Option>
                  <Option value="stapler">Stapler</Option>
                  <Option value="scissors">Scissors</Option>
                  <Option value="paperweight">Paperweight</Option>
                  <Option value="nameplate">Nameplate</Option>
                  <Option value="map">Map</Option>
                  <Option value="lamp">Lamp</Option>
                  <Option value="cup">Cup</Option>
                  <Option value="rubber_band">Rubber Band</Option>
                  <Option value="folder">Folder</Option>
                  <Option value="sticky_notes">Sticky Notes</Option>
                  <Option value="paper_clips">Paper Clips</Option>
                  <Option value="first_aid_kit">First Aid Kit</Option>
                  <Option value="other">Other</Option>
                </Select>
              </Form.Item>
            </Col>
            {itemtype === "other" && (
              <Col xs={24} md={24} lg={5}>
                <Form.Item
                  label="other item name"
                  name="otheritemtype"
                  rules={[
                    { required: true, message: "other item name required" },
                  ]}
                >
                  <Input type="text" placeholder="type item name" />
                </Form.Item>
              </Col>
            )}
            <Col xs={24} md={24} lg={5}>
              <Form.Item
                label="Unique Id"
                name="uid"
                rules={[{ required: true, message: " uid required" }]}
              >
                <Input type="text" placeholder="type unique id" />
              </Form.Item>
            </Col>
            <Col xs={24} md={24} lg={5}>
              <Form.Item
                name="installationdate"
                label="Installation Date"
                rules={[
                  {
                    required: true,
                    message: "Installation date is required",
                  },
                ]}
              >
                <DatePicker style={{ width: "100%" }} />
              </Form.Item>
            </Col>
            <Col xs={24} md={24} lg={5}>
              <Form.Item
                name="description"
                label="Description"
                rules={[{ required: true, message: "Description is required" }]}
              >
                <Input.TextArea
                  placeholder="enter description"
                  rows={1}
                  style={{ width: "100%" }}
                />
              </Form.Item>
            </Col>
            <Col xs={24} md={24} lg={4}>
              <Form.Item>
                <button
                  type="submit"
                  className="btn btn-primary"
                  style={{ width: "60%", marginLeft: "4px",marginTop:"22px"}}
                >
                  Submit
                </button>
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </div>

      <table
          className="table"
          style={{
            width: "100%",
            borderCollapse: "collapse",
            marginTop: "10px",
          }}
        >
          <thead>
            <tr>
              <th
                style={{
                  border: "2px solid black",
                  textAlign: "center",
                  padding: "10px",
                  color: "red",
                }}
              >
                Item Type
              </th>
              <th
                style={{
                  border: "2px solid black",
                  textAlign: "center",
                  padding: "10px",
                  color: "red",
                }}
              >
               Unique Id
              </th>
              <th
                style={{
                  border: "2px solid black",
                  textAlign: "center",
                  padding: "10px",
                  color: "red",
                }}
              >
                Installation Date
              </th>
              <th
                style={{
                  border: "2px solid black",
                  textAlign: "center",
                  padding: "10px",
                  color: "red",
                }}
              >
                Description
              </th>
              <th
                style={{
                  border: "2px solid black",
                  textAlign: "center",
                  padding: "10px",
                  color: "red",
                }}
              >
                Actions
              </th>
              </tr>
</thead>
<tbody>
{teacherdetails &&
              teacherdetails.map((data) => (
                <tr key={data._id}>
                  <td
                    style={{
                      border: "2px solid black",
                      textAlign: "center",
                      padding: "10px",
                    }}
                  >
                    <b>{data.itemtype}</b>
                  </td>
                  <td
                    style={{
                      border: "2px solid black",
                      textAlign: "center",
                      padding: "10px",
                    }}
                  >
                    <b>{data.uid}</b>
                  </td>
                  <td
                    style={{
                      border: "2px solid black",
                      textAlign: "center",
                      padding: "10px",
                    }}
                  >
                    {new Date(data.installationdate).toLocaleDateString()}
                  </td>
                  <td
                    style={{
                      border: "2px solid black",
                      textAlign: "center",

                      width: "350px",
                    }}
                  >
                    <div style={{ maxHeight: "150px", overflowY: "auto" }}>
                      {data.description || "N/A"}
                    </div>
                  </td>
                  <td
                    style={{
                      border: "2px solid black",
                      textAlign: "center",
                      padding: "10px",
                    }}
                  >
                    <i
                      className="fa-solid fa-trash icon"
                      style={{ cursor: "pointer", margin: "10px" }}
                      onClick={() => {
                        if (window.confirm("Do you want to delete?")) {
                          handledelete(data._id);
                        }
                      }}
                    ></i>
                    <i
                      className="fa-solid fa-pen-to-square edit"
                      style={{ cursor: "pointer", margin: "10px" }}
                      onClick={() => {
                        if (window.confirm("Do you want to edit?")) {
                          navigate(`/teacherdetaileditnon/${data._id}`);
                        }
                      }}
                    ></i>
                  </td>
                </tr>
              ))}
</tbody>
              </table>

    </Layout>
  );
};

export default Teacherdetailsnon;
