import React, { useEffect, useState } from "react";
import Layout from "../../components/Layout";
import { Form, Input, Row, Col, message, Select, DatePicker } from "antd";

import "../../styles/Newtechpagestyles.css";

import { useParams, useNavigate } from "react-router-dom";
import axios from "axios";
import moment from "moment";
import { useDispatch, useSelector } from "react-redux";
import { setRole } from "../../redux/features/roleslice";
const { Option } = Select;

const Dumpnontech = () => {
  const navigate=useNavigate();
  const roles = useSelector((State) => State.role.role);
  const dispatch = useDispatch();
  const [dumpdata, setDumpdata] = useState([]);
  const [teacherlist, setTeacherlist] = useState([]);
  const [classlist, setClasslist] = useState([]);
  const [labslist, setLabslist] = useState([]);
  const [serverlist, setServerlist] = useState([]);
  const [roomnames, setRoomnames] = useState({});
  const [tabledata, setTabledata] = useState([]);
  const [item, setItem] = useState("all");
  const [countvalue, setCountvalue] = useState();
  const [deleteitem, setDeleteitem] = useState();
  const[totalitems,setTotalitems]=useState([]);
 

  useEffect(() => {
    if (roles === "admin" || roles === "editor") {
      localStorage.setItem("role", roles);
    }
  }, [roles]);

  useEffect(() => {
    const savedRole = localStorage.getItem("role");
    if (savedRole && savedRole !== roles) {
      dispatch(setRole(savedRole)); // Sync Redux with localStorage
    }
  }, [dispatch]);

  const getalldumpdata = async () => {
    try {
      const res = await axios.get("/api/v1/tech/lab/getalldumpdata ", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });
      if (res.data.success) {
        message.success(res.data.message);
        setDumpdata(res.data.data);
      } else {
        message.error(res.data.messgae);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };
  useEffect(() => {
    getalldumpdata();
  }, []);

  const getallteacher = async () => {
    const res = await axios.get("/api/v1/tech/teacher/getallteacher", {
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    });

    if (res.data.success) {
      setTeacherlist(res.data.data);
      message.success("all teachers get");
    } else {
      message.error(res.data.message);
    }
  };

  useEffect(() => {
    getallteacher();
  }, []);

  const getallclass = async () => {
    const res = await axios.get("/api/v1/tech/getallclass", {
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    });

    if (res.data.success) {
      setClasslist(res.data.data);
      message.success("all classes get");
    } else {
      message.error(res.data.message);
    }
  };
  useEffect(() => {
    getallclass();
  }, []);
  const getalllabs = async () => {
    const res = await axios.get("/api/v1/tech/lab/getalllabs", {
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    });

    if (res.data.success) {
      setLabslist(res.data.data);
      message.success("all labs get");
    } else {
      message.error(res.data.message);
    }
  };

  useEffect(() => {
    getalllabs();
  }, []);
  const getallserver = async () => {
    const res = await axios.get("/api/v1/tech/server/getallserver", {
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    });

    if (res.data.success) {
      setServerlist(res.data.data);
      message.success("all server get");
    } else {
      message.error(res.data.message);
    }
  };

  useEffect(() => {
    getallserver();
  }, []);

  const roomnamess = async () => {
    const roomnames = {};

    const alldumpdata = dumpdata;
    for (let data of alldumpdata) {
      roomnames[data._id] = await roomname(data.roomid, data.roomtype);
    }
    setRoomnames(roomnames);
  };

  const roomname = async (roomid, roomtype) => {
    const teachers = teacherlist;
    const labs = labslist;
    const classes = classlist;
    const server = serverlist;
    if (roomtype === "teacher") {
      for (let data of teachers) {
        if (data._id === roomid) {
          return data.teachername;
        }
      }
    } else if (roomtype === "class") {
      for (let data of classes) {
        if (data._id === roomid) {
          return data.classname;
        }
      }
    } else if (roomtype === "lab") {
      for (let data of labs) {
        if (data._id === roomid) {
          return data.labname;
        }
      }
    } else if (roomtype === "server") {
      for (let data of server) {
        if (data._id === roomid) {
          return data.roomname;
        }
      }
    }
  };
  useEffect(() => {
    roomnamess();
  }, [dumpdata, teacherlist, classlist, labslist, serverlist]);

  const handledelete = async (id) => {
    try {
      const res = await axios.post(
        "/api/v1/tech/lab/deleteonedumpdata",
        {
          id,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      if (res.data.success) {
        message.success(res.data.message);
        window.location.reload();
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  useEffect(() => {
    let tabledatas;
    if (item === "all") {
      tabledatas = dumpdata;
    } else if (item === "other") {
      const excludeitem = [
        "whiteboard",
        "marker",
        "chalk",
        "duster",
        "notice_board",
        "bookshelf",
        "clock",
        "chair",
        "desk",
        "pen_holder",
        "paper_stack",
        "file_cabinet",
        "cupboard",
        "curtains",
        "trash_bin",
        "water_bottle",
        "notepad",
        "globe",
        "poster",
        "calendar",
        "plant",
        "student_chair",
        "bulletin_board",
        "stapler",
        "scissors",
        "paperweight",
        "nameplate",
        "map",
        "lamp",
        "cup",
        "rubber_band",
        "folder",
        "sticky_notes",
        "paper_clips",
        "first_aid_kit",
      ];
      tabledatas = dumpdata.filter(
        (data) => !excludeitem.includes(data.itemtype)
      );
    } else {
      tabledatas = dumpdata.filter((data) => data.itemtype === item);
    }
    setTabledata(tabledatas);
    setCountvalue(tabledatas.length);
  }, [item, dumpdata]);

  const itemchange = (value) => {
    setItem(value);
    //tabledata(item,labnums);
  };

   useEffect(()=>{
    const allteachers=[...new Set(dumpdata.map(data=>data.itemtype))];
    setTotalitems(allteachers);
      },[dumpdata]);


  const deleteitemchange = (value) => {
    setDeleteitem(value);
    //tabledata(item,labnums);
  };

  const deletedata = async () => {
    try {
         
      const res = await axios.post(
        "/api/v1/tech/lab/deletedata",
        {
          item: deleteitem,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );

      if (res.data.success) {
        message.success(res.data.message);
        window.location.reload();
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  return (
    <Layout>
      <div
        style={{
          alignItems: "center",
          justifyContent: "center",
          display: "flex",
          fontSize: "24px",
          fontFamily: "bold",
        }}
      >
        <p>DUMP ROOM NON TECHNICAL</p>
      </div>
      <div
        className="d-flex justify-content-center align-items-center gap-2"
        style={{ width: "100%" }}
      >
        <span>SELECT ITEM : </span>
        <Select
          placeholder="choose an item"
          onChange={itemchange}
          style={{ width: "150px" }}
        >
          {[
            "all",
            "other",
            "whiteboard",
            "marker",
            "chalk",
            "duster",
            "notice_board",
            "bookshelf",
            "clock",
            "chair",
            "desk",
            "pen_holder",
            "paper_stack",
            "file_cabinet",
            "cupboard",
            "curtains",
            "trash_bin",
            "water_bottle",
            "notepad",
            "globe",
            "poster",
            "calendar",
            "plant",
            "student_chair",
            "bulletin_board",
            "stapler",
            "scissors",
            "paperweight",
            "nameplate",
            "map",
            "lamp",
            "cup",
            "rubber_band",
            "folder",
            "sticky_notes",
            "paper_clips",
            "first_aid_kit",
          ].map((data) => (
            <Option value={data}>{data}</Option>
          ))}
        </Select>
        <span>DELETE BY ITEM : </span>
        <Select
          placeholder="choose an item"
          onChange={deleteitemchange}
          style={{ width: "150px" }}
        >
            <Option value="all">All</Option>
         {totalitems.map((data) => (
            <Option value={data}>{data}</Option>
          ))}
        </Select>
        
        <button
          style={{
            borderRadius: "10px",
            cursor: "pointer",
            color: "white",
            backgroundColor: "red",
            padding: "6px 8px",
          }}
          onClick={()=>{if(window.confirm("are you sure to delete?")){deletedata()}}}
            
            
        >
          Delete
        </button>
      </div>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          width: "100%",
          position: "relative",
          justifyContent: "space-between",
        }}
      >
        <button
          onClick={() => window.print()}
          style={{
            borderRadius: "10px",
            cursor: "pointer",
            color: "white",
            backgroundColor: "blue",
            padding: " 6px 10px",
            border: "none",
          }}
        >
          PRINT
        </button>

        <span
          style={{
            fontWeight: "bold",
            fontSize: "24px",
            position: "absolute",
            left: "50%",
          }}
        >
          Total Count:{countvalue}
        </span>

        <button
          onClick={() =>{navigate("/purchaselist")}}
          style={{
            borderRadius: "10px",
            cursor: "pointer",
            color: "white",
            backgroundColor: "green",
            padding: " 6px 10px",
            border: "none",
          }}
        >
          Generate Purchase List
        </button>
      </div>

      <table
        className="table"
        style={{
          width: "100%",
          borderCollapse: "collapse",
          marginTop: "10px",
        }}
      >
        <thead>
          <tr>
            <th
              style={{
                border: "2px solid black",
                textAlign: "center",
                padding: "10px",
                color: "red",
              }}
            >
              Item Type
            </th>
            <th
              style={{
                border: "2px solid black",
                textAlign: "center",
                padding: "10px",
                color: "red",
              }}
            >
              Unique Id
            </th>
            <th
              style={{
                border: "2px solid black",
                textAlign: "center",
                padding: "10px",
                color: "red",
              }}
            >
              Room Name
            </th>
            <th
              style={{
                border: "2px solid black",
                textAlign: "center",
                padding: "10px",
                color: "red",
              }}
            >
              DE-Installation Date
            </th>
            <th
              style={{
                border: "2px solid black",
                textAlign: "center",
                padding: "10px",
                color: "red",
              }}
            >
              Description
            </th>
            <th
              style={{
                border: "2px solid black",
                textAlign: "center",
                padding: "10px",
                color: "red",
              }}
            >
              Actions
            </th>
          </tr>
        </thead>
        <tbody>
          {tabledata &&
            tabledata.map((data) => (
              <tr key={data._id}>
                <td
                  style={{
                    border: "2px solid black",
                    textAlign: "center",
                    padding: "10px",
                  }}
                >
                  <b>{data.itemtype}</b>
                </td>
                <td
                  style={{
                    border: "2px solid black",
                    textAlign: "center",
                    padding: "10px",
                  }}
                >
                  <b>{data.uid}</b>
                </td>
                <td
                  style={{
                    border: "2px solid black",
                    textAlign: "center",
                    padding: "10px",
                  }}
                >
                  <b>{roomnames[data._id]}</b>
                </td>
                <td
                  style={{
                    border: "2px solid black",
                    textAlign: "center",
                    padding: "10px",
                  }}
                >
                  {data.deinstallationdate
                    ? new Date(data.deinstallationdate).toLocaleDateString()
                    : "N/A"}
                </td>
                <td
                  style={{
                    border: "2px solid black",
                    textAlign: "center",

                    width: "350px",
                  }}
                >
                  <div style={{ maxHeight: "150px", overflowY: "auto" }}>
                    {data.description || "N/A"}
                  </div>
                </td>
                <td
                  style={{
                    border: "2px solid black",
                    textAlign: "center",
                    padding: "10px",
                  }}
                >
                  <i
                    className="fa-solid fa-trash icon"
                    style={{ cursor: "pointer", margin: "10px" }}
                    onClick={() => {
                      if (window.confirm("Do you want to delete?")) {
                        handledelete(data._id);
                      }
                    }}
                  ></i>
                </td>
              </tr>
            ))}
        </tbody>
      </table>
    </Layout>
  );
};

export default Dumpnontech;
