import React,{useEffect} from 'react'
import { useNavigate } from "react-router-dom";
import "../styles/Homepagestyles.css";
 
import { useDispatch, useSelector } from "react-redux";
import { setRole } from "../redux/features/roleslice";

const Layout2 = ({ children }) => {
  const roles=useSelector(state=>state.role.role);
  const dispatch = useDispatch();
  
       useEffect(() => {
          if (roles === "admin" || roles === "editor") {
            localStorage.setItem("role", roles);
          }else if(roles==="viewer"){
            localStorage.setItem("role", roles);
          }
        }, [roles]);
      
        useEffect(() => {
          const savedRole = localStorage.getItem("role");
          if (savedRole && savedRole !== roles) {
            dispatch(setRole(savedRole)); // Sync Redux with localStorage
          }
        }, [dispatch]);
  
  const navigate = useNavigate();
  const handlelogout = async () => {
    if (localStorage.getItem("token")) {
      localStorage.clear();
      navigate("/login");
    }
  };

 
  return (
    <>
    {roles==="viewer"?(
       <div className="logout">
          <button
          className="btn btn-primary"
          onClick={() => {
            navigate("/techsum");
          }}
        >
          TECHNICAL SUMMARY
        </button>
        <button
          className="btn btn-primary"
          onClick={() => {
            navigate("/non-tech-sum");
          }}
        >
          NON-TECHNICAL SUMMARY
        </button>
        <button className="btn btn-danger" onClick={handlelogout}>
          LOGOUT
        </button>
       </div>
    ):(
<div className="logout">
        <button
          className="btn btn-primary"
          onClick={() => {
            navigate("/");
          }}
        >
          HOME
        </button>
        <button
          className="btn btn-primary"
          onClick={() => {
            navigate("/techsum");
          }}
        >
          TECHNICAL SUMMARY
        </button>
        <button
          className="btn btn-primary"
          onClick={() => {
            navigate("/non-tech-sum");
          }}
        >
          NON-TECHNICAL SUMMARY
        </button>
        <button className="btn btn-danger" onClick={handlelogout}>
          LOGOUT
        </button>
      </div>
    )}
      
      {children}
      
    </>
  )
}

export default Layout2
