import React, { useEffect, useState } from "react";
import Layout2 from "../../components/Layout2";
import { message, Select, Table } from "antd";
import axios, { all } from "axios";

const { Option } = Select;

const Labsum = () => {
  const [labs, setLabs] = useState([]);
  const [labdetails, setlabdetails] = useState([]);

  const [labnumbers, setLabnumbers] = useState({});
const [totallabs,setTotallabs]=useState([]);
const[labnums,setLabnums]=useState(0);
const[sortorder,setSortorder]=useState('oldtonew')

  const [item, setItem] = useState("all");
  const [displaydata, setDisplaydata] = useState();
  const [buildings,setBuildings]=useState([]);
  const [buildingname,setBuildingname]=useState('none');
  const [buildingobject,setBuildingobject]=useState({});

  const[labname,setLabname]=useState('all');
  const[alllabname,setAlllabname]=useState({});

  
  

  const getalllabs = async () => {
    try {
      const res = await axios.get("/api/v1/tech/lab/alllabs", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });
      if (res.data.success) {
        message.success(res.data.message);
        setLabs(res.data.data);
      } else {
        message.error(res.data.messgae);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  const getlabdetails = async () => {
    try {
      const res = await axios.get("/api/v1/tech/lab/labdetails", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });
      if (res.data.success) {
        message.success(res.data.message);
        setlabdetails(res.data.data);
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  const labnos = async () => {
    const labno = {};
 
    const alllabdetails = labdetails;
    for (let data of alllabdetails) {
      labno[data._id] = await labnumber(data.labid);
    }
    setLabnumbers(labno);
  };

  const labnumber = async (labid) => {
    const alllabs = labs;
    for (let data of alllabs) {
      if (data._id === labid) {
        return data.labno;
      }
    }
  };

  const buildingnames = async () => {
    const buildings = {};
 
    const alllabdetails = labdetails;
    for (let data of alllabdetails) {
      buildings[data._id] = await b_names(data.labid);
    }
    setBuildingobject(buildings);
  };

  const b_names = async (labid) => {
    const alllabs = labs;
    for (let data of alllabs) {
      if (data._id === labid) {
        return data.buildingname;
      }
    }
  };

  const labnames = async () => {
    const buildings = {};
 
    const alllabdetails = labdetails;
    for (let data of alllabdetails) {
      buildings[data._id] = await lab_names(data.labid);
    }
    setAlllabname(buildings);
  };

  const lab_names = async (labid) => {
    const alllabs = labs;
    for (let data of alllabs) {
      if (data._id === labid) {
        return data.labname;
      }
    }
  };

  

  useEffect(() => {
    getlabdetails();
  }, []);

  useEffect(() => {
    getalllabs();
  }, []);

  useEffect(() => {
    labnos();
  }, [labdetails, labs]);

  useEffect(() => {
    buildingnames();
  }, [labdetails, labs]);

  
  useEffect(() => {
    labnames();
  }, [labdetails, labs]);

  useEffect(()=>{
const labnums=[...new Set(labs.map(data=>data.labname))];
setTotallabs(labnums);
  },[labs]);

  useEffect(()=>{
const buildings=[...new Set(labs.map(data=>data.buildingname))];
setBuildings(buildings);
  },[labs])

  const initialprint = async () => {
    if (item === "all") {
      const tabledatas = labdetails.map((data) => ({
        key: data._id,
        product:
          data.itemtype === "desktop" ? data.desktopnumber : data.itemtype,
        date: new Date(data.installationdate).toLocaleDateString(),
        description:
        data.description +" "+
        (data.desktopnumber ? `desktopnumber=${data.desktopnumber}` : " ") +" "+
        (data.ipaddress ? `ipaddress=${data.ipaddress}` : "") +" "+
        (data.monitornumber ? ` monitornumber=${data.monitornumber}` : "") +" "+
        (data.monitorname ? ` monitorname=${data.monitorname}` : "") +" "+
        (data.mousenumber ? ` mousenumber=${data.mousenumber}` : "") +" "+
        (data.mousename ? ` mousename=${data.mousename}` : "") +" "+
        (data.keyboardnumber
          ? ` keyboardnumber=${data.keyboardnumber}`
          : "") +" "+
        (data.keyboardname ? ` keyboardname=${data.keyboardname}` : "") +" "+
        (data.cpunumber ? ` cpunumber=${data.cpunumber}` : "") +" "+
        (data.cpuname ? ` cpuname=${data.cpuname}` : ""),
        labname: alllabname[data._id]
      }));
      setDisplaydata(tabledatas);
    }
  };
  useEffect(() => {
    initialprint();
  }, [labdetails,alllabname]);

  const tabledata = (item, lab_name,order,alllabname) => {
    let displaydatas;
    if (item === 'other') {
      if (lab_name==='all') {
        
        const excludeitem = ["speaker", "desktop", "microphone"];
        displaydatas = labdetails.filter(
          (data) => !excludeitem.includes(data.itemtype)
        );
      }else {
        const excludeitem = ["speaker", "desktop", "microphone"];
        displaydatas = labdetails.filter(
          (data) =>
            !excludeitem.includes(data.itemtype) &&
            alllabname[data._id]===lab_name
        );
      } 
      
    } else if (item === "all") {
      if (lab_name==='all') {
        displaydatas = labdetails;  
      } else {
        displaydatas = labdetails.filter(
          (data) => alllabname[data._id]===lab_name
        );
      } 
    } else {
      if (lab_name==='all') {
        displaydatas = labdetails.filter((data) => data.itemtype === item );
      } else {
        displaydatas = labdetails.filter(
          (data) => data.itemtype === item && alllabname[data._id]===lab_name
        );
      } 
      }
    
  
displaydatas.sort((a,b)=>{
  const dateA=new Date(a.installationdate);
  const dateB=new Date(b.installationdate);
  return order==='newtoold'?dateB-dateA:dateA-dateB;
});

    const tabledatas = displaydatas.map((data) => ({
      key: data._id,
      product:
        data.itemtype === "desktop" ? data.desktopnumber : data.itemtype,
      date: new Date(data.installationdate).toLocaleDateString(),
      description:
        data.description +' '+
        (data.desktopnumber ? ` desktopnumber=${data.desktopnumber}` : "") +' '+
        (data.ipaddress ? ` ipaddress=${data.ipaddress}` : "") +' '+
        (data.monitornumber ? ` monitornumber=${data.monitornumber}` : "") +' '+
        (data.monitorname ? ` monitorname=${data.monitorname}` : "") +' '+
        (data.mousenumber ? ` mousenumber=${data.mousenumber}` : "") +' '+
        (data.mousename ? ` mousename=${data.mousename}` : "") +' '+
        (data.keyboardnumber
          ? ` keyboardnumber=${data.keyboardnumber}`
          : "") +' '+
        (data.keyboardname ? ` keyboardname=${data.keyboardname}` : "") +' '+
        (data.cpunumber ? ` cpunumber=${data.cpunumber}` : "") +' '+
        (data.cpuname ? ` cpuname=${data.cpuname}` : ""),
      labname: alllabname[data._id]
    }));
    setDisplaydata(tabledatas);  
  };
  

  const columns =
    labname === "all"
      ? [
          { title: "DESKTOP NUMBER/ITEM NAME", dataIndex: "product", key: "product" },
          { title: "INSTALLATION DATE", dataIndex: "date", key: "date" },
          {
            title: "DESCRIPTION/DESKTOP INFO.",
            dataIndex: "description",
            key: "description",
          },
          { title: "LAB NAME", dataIndex: "labname", key: "labname" },
        ]
      : [
          {
            title: "ITEM NAME/DESKTOP NUMBER",
            dataIndex: "product",
            key: "product",
          },
          { title: "INSTALLATION DATE", dataIndex: "date", key: "date" },
          {
            title: "DESCRIPTION",
            dataIndex: "description",
            key: "description",
          },
        
        ];
const itemchange=(value)=>{
setItem(value);
//tabledata(item,labnums);
}

const labnamechange=(value)=>{
  setLabname(value);
 // tabledata(item,labnums);
}
useEffect(()=>{
tabledata(item,labname,sortorder,alllabname);
},[item,labname,sortorder,alllabname])

  return (
    <>
   
      <Layout2>
      <div style={{alignItems:'center',justifyContent:'center',display:'flex',fontSize:'24px',fontFamily:'bold'}}><p>LAB SUMMARY</p></div>
        <div className="d-flex justify-content-center align-items-center gap-2" style={{width:"100%"}}>
   
        <span>SELECT ITEM : </span>
          <Select placeholder="choose an item" onChange={itemchange}  style={{width:"150px"}}>
            <Option value="desktop">Desktop</Option>
            <Option value="microphone">Microphone</Option>
            <Option value="speaker">Speaker</Option>
            <Option value="other">Other</Option>
            <Option value="all">All</Option>
          </Select>
          <span>SELECT LAB : </span>
           <Select placeholder="choose lab name" onChange={labnamechange}  style={{width:"150px"}}>
           <Option value="all">All</Option>
           { totallabs.map((data)=>(
            <Option value={data}>{data}</Option>
           ))}

           </Select>
 

           <span>SORT ORDER</span>
           <Select placeholder='select sort order' onChange={(value)=>{setSortorder(value)}}>
            <Option value='newtoold'>New to Old</Option>
            <Option value='oldtonew'>Old to New</Option>
           </Select>
            
        </div>
        <button onClick={()=>{window.print()}} style={{marginLeft:'5px',borderRadius:"10px",cursor:'pointer' }}>PRINT</button>
        <Table columns={columns} dataSource={displaydata} pagination={false} />
      </Layout2>
    </>
  );
};

export default Labsum;
