import React, { useState, useEffect } from "react";
import Layout from "../../components/Layout";
import {
  Form,
  Input,
  Row,
  Col,
  message,
  Select,
  DatePicker,
  Divider,
  Button,
} from "antd";
import "../../styles/Techpagestyles.css";
import { useParams, useNavigate } from "react-router-dom";
import axios from "axios";
import moment from "moment";
import { useDispatch, useSelector } from "react-redux";
import { setRole } from "../../redux/features/roleslice";

const { Option } = Select;

const Labdetailedit = () => {
  const [form] = Form.useForm();
  const [labdetail, setLabdetail] = useState(null);
  const [itemtype, setItemtype] = useState(null);
  const params = useParams();
  const navigate = useNavigate();

  const roles = useSelector((State) => State.role.role);
  const dispatch = useDispatch();
  
   useEffect(() => {
      if (roles === "admin" || roles === "editor") {
        localStorage.setItem("role", roles);
      }
    }, [roles]);
  
    useEffect(() => {
      const savedRole = localStorage.getItem("role");
      if (savedRole && savedRole !== roles) {
        dispatch(setRole(savedRole)); // Sync Redux with localStorage
      }
    }, [dispatch]);

  const getlabdetail = async () => {
    try {
      const res = await axios.post(
        "/api/v1/tech/lab/getlabdetailforedit",
        {
          labdetailid: params.labdetailid,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      if (res.data.success) {
        message.success(res.data.message);
        setLabdetail(res.data.data);
        const { installationdate, ...otherdata } = res.data.data;
        form.setFieldsValue({
          ...otherdata,
          installationdate: moment(installationdate),
        });
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  useEffect(() => {
    getlabdetail();
  }, []);

  const handlefinish = async (values) => {
    if (values.itemtype === "other" && values.otheritemtype) {
      values.itemtype = values.otheritemtype;
      delete values.otheritemtype;
    }
    values.labdetailid = params.labdetailid;

    try {
      const res = await axios.post("/api/v1/tech/lab/updatedetail", values, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });
      if (res.data.success) {
        message.success(res.data.message);
        window.history.back();
      } else {
        message.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      message.error("spmething went wrong");
    }
  };

  return (
    <>
      <Layout>
        <div
          className="form-div"
          style={{
            border: "1px solid black",
            width: "300px",
            float: "left",
            marginTop: "10px",
          }}
        >
          <Form
            layout="vertical"
            onFinish={handlefinish}
            className="m-2"
            form={form}
          >
            <h3 className="text-center">Edit Detail</h3>
            <Row gutter={20}>
              <Col xs={24} md={24}>
                <Form.Item
                  name="itemtype"
                  label="Item Type"
                  rules={[{ required: true, message: "Item type is required" }]}
                >
                  <Select
                    placeholder="select item type"
                    onChange={(value) => setItemtype(value)}
                  >
                    <Option value="speaker">Speaker</Option>
                    <Option value="mic">Microphone</Option>
                    <Option value="desktop">Desktop Computer</Option>
                    <Option value="other">Other</Option>
                  </Select>
                </Form.Item>
              </Col>
              {itemtype === "other" && (
                <Col xs={24} md={24}>
                  <Form.Item
                    label="other item name"
                    name="otheritemtype"
                    rules={[
                      { required: true, message: "other item name required" },
                    ]}
                  >
                    <Input type="text" placeholder="type item name" />
                  </Form.Item>
                </Col>
              )}
              <Col xs={24} md={24}>
                <Form.Item
                  name="installationdate"
                  label="Installation Date"
                  rules={[
                    {
                      required: true,
                      message: "Installation date is required",
                    },
                  ]}
                >
                  <DatePicker style={{ width: "100%" }} />
                </Form.Item>
              </Col>
              <Col xs={24} md={24}>
                <Form.Item
                  name="description"
                  label="Description"
                  rules={[
                    { required: true, message: "Description is required" },
                  ]}
                >
                  <Input.TextArea
                    placeholder="enter description"
                    rows={4}
                    style={{ width: "100%" }}
                  />
                </Form.Item>
              </Col>

              {labdetail?.itemtype === "desktop" && (
                <div style={{ width: "100%" }}>
                  <Col xs={24} md={24}>
                    <Form.Item
                      name="desktopnumber"
                      label="Desktop Number"
                      rules={[
                        {
                          required: true,
                          message: "Desktop number is required",
                        },
                      ]}
                    >
                      <Input
                        type="text"
                        style={{ width: "100%" }}
                        placeholder="enter desktop number"
                      />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24}>
                    <Form.Item
                      name="ipaddress"
                      label="IP Address"
                      rules={[
                        {
                          required: true,
                          message: "Ip address is required",
                        },
                      ]}
                    >
                      <Input
                        type="text"
                        style={{ width: "100%" }}
                        placeholder="enter ip address"
                      />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24}>
                    <Form.Item
                      name="monitornumber"
                      label="Monitor Number"
                      rules={[
                        {
                          required: true,
                          message: "Monitor number is required",
                        },
                      ]}
                    >
                      <Input type="text" placeholder="enter monitor number" />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24}>
                    <Form.Item
                      name="monitorname"
                      label="Monitor Attributes"
                      rules={[{ required: true }]}
                    >
                      <Input type="text" placeholder="Brand, Model, Size" />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24}>
                    <Form.Item
                      name="mousenumber"
                      label="Mouse Number"
                      rules={[
                        { required: true, message: "Mouse number is required" },
                      ]}
                    >
                      <Input type="text" placeholder="enter mouse number" />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24}>
                    <Form.Item
                      name="mousename"
                      label="Mouse Attributes"
                      rules={[{ required: true }]}
                    >
                      <Input type="text" placeholder="Brand and Model" />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24}>
                    <Form.Item
                      name="keyboardnumber"
                      label="Keyboard Number"
                      rules={[
                        { required: true, message: "keyboard no required" },
                      ]}
                    >
                      <Input type="text" placeholder="enter keyboard number" />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24}>
                    <Form.Item
                      name="keyboardname"
                      label="Keyboard Attributes"
                      rules={[{ required: true }]}
                    >
                      <Input type="text" placeholder="Brand and model" />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24}>
                    <Form.Item
                      name="cpunumber"
                      label="CPU Number"
                      rules={[
                        { required: true, message: "CPU number is required" },
                      ]}
                    >
                      <Input type="text" placeholder="enter cpu number" />
                    </Form.Item>
                  </Col>
                  <Col xs={24} md={24}>
                    <Form.Item
                      name="cpuname"
                      label="CPU Attributes"
                      rules={[{ required: true }]}
                    >
                      <Input type="text" placeholder="Brand, Model, Speed" />
                    </Form.Item>
                  </Col>
                </div>
              )}

              <Col xs={24} md={24}>
                <Form.Item>
                  <button
                    type="submit"
                    className="btn btn-primary"
                    style={{ width: "100%", marginLeft: "0px" }}
                  >
                    Submit
                  </button>
                </Form.Item>
              </Col>
            </Row>
          </Form>
        </div>
      </Layout>
    </>
  );
};

export default Labdetailedit;
