import React, { useEffect, useState } from "react";
import Layout from "../../components/Layout";
import { Form, Input, Row, Col, message } from "antd";
import axios from "axios";
import "../../styles/Techpagestyles.css";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { setRole } from "../../redux/features/roleslice";

const Classpage_non = () => {
    const roles = useSelector((State) => State.role.role);
    const dispatch = useDispatch();
  const navigate = useNavigate();
  const [classlist, setClasslist] = useState([]);

    useEffect(() => {
      if (roles === "admin" || roles === "editor") {
        localStorage.setItem("role", roles);
      }
    }, [roles]);
  
    useEffect(() => {
      const savedRole = localStorage.getItem("role");
      if (savedRole && savedRole !== roles) {
        dispatch(setRole(savedRole)); // Sync Redux with localStorage
      }
    }, [dispatch]);
    
  const handleform = async (values) => {
    try {
      const res = await axios.post("/api/v1/tech/addclass", values);

      if (res.data.success) {
        message.success(res.data.message);
      } else {
        message.error(res.data.message);
      }
      window.location.reload();
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };
  const getallclass = async () => {
    const res = await axios.get("/api/v1/tech/getallclass", {
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    });

    if (res.data.success) {
      setClasslist(res.data.data);
      message.success("all classes get");
    } else {
      message.error(res.data.message);
    }
  };
  useEffect(() => {
    getallclass();
  }, []);

  const handledelete = async (id) => {
    try {
      // eslint-disable-next-line no-restricted-globals
      var result = confirm("really want to delete");
      if (result) {
        const res = await axios.post(
          "/api/v1/tech/deleteclass",
          { id },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        if (res.data.success) {
          message.success(res.data.message);
        } else {
          message.error(res.data.message);
        }
        window.location.reload();
      }
    } catch (error) {
      console.log(error);
      message.error("something went wrong");
    }
  };

  return (
    <>
      <Layout>
        <div className="">
          <Form layout="vertical" onFinish={handleform} className="m-2">
            <h3 className="text-center">Class Rooms</h3>
            <Row gutter={20}>
              <Col xs={24} md={24} lg={5}>
                <Form.Item
                  label="Class name"
                  name="classname"
                  rules={[{ required: true }]}
                >
                  <Input type="text" placeholder="type class name" />
                </Form.Item>
              </Col>
              <Col xs={24} md={24} lg={5}>
                <Form.Item
                  label="Class number"
                  name="classno"
                  rules={[{ required: true }]}
                >
                  <Input type="number" placeholder="type class number" />
                </Form.Item>
              </Col>
              <Col xs={24} md={24} lg={5}>
                <Form.Item
                  label="Building name"
                  name="buildingname"
                  rules={[{ required: true }]}
                >
                  <Input type="text" placeholder="type building name" />
                </Form.Item>
              </Col>
              <Col xs={24} md={24} lg={5}>
                <Form.Item
                  label="Floor number"
                  name="floorno"
                  rules={[{ required: true }]}
                >
                  <Input type="number" placeholder="type floor number" />
                </Form.Item>
              </Col>
              <Col xs={24} md={24} lg={4}>
                <button
                  className="btn btn-primary"
                  style={{ width: "180px", marginTop: "25px" }}
                  type="submit"
                >
                  Submit
                </button>
              </Col>
            </Row>
          </Form>
        </div>

        {classlist &&
          classlist.map((data) => {
            return (
              <div className="card m-2 cardstyle">
                <div
                  style={{ cursor: "pointer" }}
                  className="card-header"
                  onClick={() => {
                    navigate(`/classdetailsnon/${data._id}`);
                  }}
                >
                  Class Name:
                  <b> {data.classname}</b>
                </div>

                <div className="card-body p-2">
                  <p>
                    <b>Class No:</b>
                    {data.classno}
                  </p>
                  <p>
                    <b>Building Name:</b>
                    {data.buildingname}
                  </p>
                  <p>
                    <b>Floor No:</b>
                    {data.floorno}
                  </p>
                  <p>
                    <button
                      onClick={() => {
                        // eslint-disable-next-line no-restricted-globals
                        let result = confirm("really want to edit");
                        if (result) {
                          navigate(`/classeditnon/${data._id}`);
                        }
                      }}
                      className="btn btn-primary"
                    >
                      Edit
                    </button>
                    <button
                      onClick={() => {
                        handledelete(data._id);
                      }}
                      className="btn btn-danger"
                    >
                      Delete
                    </button>
                  </p>
                </div>
              </div>
            );
          })}
      </Layout>
    </>
  );
};

export default Classpage_non;

