import React, { useEffect, useState } from "react";
import Layout2 from "../../components/Layout2";
import { message, Select, Table } from "antd";
import axios from "axios";


const { Option } = Select;

const Classsum = () => {
    const [classes,setClasses] = useState([]);

    const [classdetails, setClassdetails] = useState([]);
  
    const [classnumbers, setClassnumbers] = useState({});
  const [totalclass,setTotalclass]=useState([]);
  const[classnums,setClassnums]=useState(0);
  const[sortorder,setSortorder]=useState('oldtonew');
  
    const [item, setItem] = useState("all");
    const [displaydata, setDisplaydata] = useState();
      const [buildings,setBuildings]=useState([]);
      const [buildingname,setBuildingname]=useState('none');
      const [buildingobject,setBuildingobject]=useState({});
    
      const [classname,setClassname]=useState('all');
      const [allclassname,setAllclassname]=useState({});
  
    const getallclass = async () => {
      try {
        const res = await axios.get("/api/v1/tech/getallclass",{
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });
        if (res.data.success) {
          message.success(res.data.message);
          setClasses(res.data.data);
        } else {
          message.error(res.data.messgae);
        }
      } catch (error) {
        console.log(error);
        message.error("something went wrong");
      }
    };
  
    const getclassdetails = async () => {
      try {
        const res = await axios.get("/api/v1/tech/getallclassdetails", {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });
        if (res.data.success) {
          message.success(res.data.message);
          setClassdetails(res.data.data);
        } else {
          message.error(res.data.message);
        }
      } catch (error) {
        console.log(error);
        message.error("something went wrong");
      }
    };
  
    const classnos = async () => {
      const classno = {};
   
      const allclassdetails = classdetails;
      for (let data of allclassdetails) {
        classno[data._id] = await classnumber(data.classid);
      }
      setClassnumbers(classno);
    };
  
    const classnumber = async (classid) => {
      const allclass = classes;
      for (let data of allclass) {
        if (data._id === classid) {
          return data.classno;
        }
      }
    };
  
    const buildingnames = async () => {
      const buildings = {};
   
      const allclassdetails = classdetails;
      for (let data of allclassdetails) {
        buildings[data._id] = await b_names(data.classid);
      }
      setBuildingobject(buildings);
    };
  
    const b_names = async (classid) => {
      const allclass = classes;
      for (let data of allclass) {
        if (data._id === classid) {
          return data.buildingname;
        }
      }
    };

    const classnames = async () => {
      const buildings = {};
   
      const allclassdetails = classdetails;
      for (let data of allclassdetails) {
        buildings[data._id] = await class_names(data.classid);
      }
      setAllclassname(buildings);
    };
  
    const class_names = async (classid) => {
      const allclass = classes;
      for (let data of allclass) {
        if (data._id === classid) {
          return data.classname;
        }
      }
    };

    useEffect(() => {
      getclassdetails();
    }, []);
  
    useEffect(() => {
      getallclass();
    }, []);
  
    useEffect(() => {
      classnos();
    }, [classdetails, classes]);

    useEffect(() => {
        classnames();
      }, [classdetails, classes]);
  
    useEffect(()=>{
  const classnums=[...new Set(classes.map(data=>data.classname))];
 
  setTotalclass(classnums);

    },[classes]);

      useEffect(()=>{
    const buildings=[...new Set(classes.map(data=>data.buildingname))];
    setBuildings(buildings);
      },[classes])

   
  
    const initialprint = async () => {
        if (item === "all") {
          const tabledatas = classdetails.map((data) => ({
            key: data._id,
            product:
              data.itemtype === "desktop" ? data.desktopnumber : data.itemtype,
            date: new Date(data.installationdate).toLocaleDateString(),
            description:
            data.description +" "+
            (data.desktopnumber ? `desktopnumber=${data.desktopnumber}` : " ") +" "+
            (data.ipaddress ? `ipaddress=${data.ipaddress}` : "") +" "+
            (data.monitornumber ? ` monitornumber=${data.monitornumber}` : "") +" "+
            (data.monitorname ? ` monitorname=${data.monitorname}` : "") +" "+
            (data.mousenumber ? ` mousenumber=${data.mousenumber}` : "") +" "+
            (data.mousename ? ` mousename=${data.mousename}` : "") +" "+
            (data.keyboardnumber
              ? ` keyboardnumber=${data.keyboardnumber}`
              : "") +" "+
            (data.keyboardname ? ` keyboardname=${data.keyboardname}` : "") +" "+
            (data.cpunumber ? ` cpunumber=${data.cpunumber}` : "") +" "+
            (data.cpuname ? ` cpuname=${data.cpuname}` : ""),
            classname: allclassname[data._id],
          }));
          setDisplaydata(tabledatas);
        }
      };
      useEffect(() => {
        initialprint();
      }, [classdetails, allclassname]);
    
      const tabledata = (item, classname,order,allclassname) => {
        let displaydatas;
        if (item === 'other') {
          if (classnums === 0 && buildingname==='none') {
            
            const excludeitem = ["speaker","microphone","smartboard","printer","projector"];
            displaydatas = classdetails.filter(
              (data) => !excludeitem.includes(data.itemtype)
            );
          } else if(classnums===0 && buildingname!=='none'){
            const excludeitem = ["speaker","microphone","smartboard","printer","projector"];
            displaydatas = classdetails.filter(
              (data) =>
                !excludeitem.includes(data.itemtype) &&
                buildingobject[data._id] === buildingname
            );
          }else if(classnums!==0 && buildingname==='none'){
            const excludeitem = ["speaker","microphone","smartboard","printer","projector"];
            displaydatas = classdetails.filter(
              (data) =>
                !excludeitem.includes(data.itemtype) &&
                classnumbers[data._id] === classnums
            );
          }else if(classnums!==0 && buildingname!=='none') {
            const excludeitem = ["speaker","microphone","smartboard","printer","projector"];
            displaydatas = classdetails.filter(
              (data) =>
                !excludeitem.includes(data.itemtype) &&
                classnumbers[data._id] === classnums && buildingobject[data._id]===buildingname
            );
          }
        } else if (item === "all") {
          if (classnums === 0 && buildingname==='none') {
            displaydatas = classdetails;  
          } else if(classnums===0 && buildingname!=='none'){
            displaydatas = classdetails.filter(
              (data) => buildingobject[data._id] === buildingname
            );
          }else if(classnums!==0 && buildingname==='none'){
            displaydatas = classdetails.filter(
              (data) => classnumbers[data._id] === classnums
            );
          }else if(classnums!==0 && buildingname!=='none'){
            displaydatas = classdetails.filter(
              (data) => classnumbers[data._id] === classnums && buildingobject[data._id]===buildingname
            );
          }
        } else {
          if (classnums === 0 && buildingname==='none') {
            displaydatas = classdetails.filter((data) => data.itemtype === item);
          } else if(classnums===0 && buildingname!=='none'){
            displaydatas = classdetails.filter(
              (data) => data.itemtype === item && buildingobject[data._id] === buildingname
            );
          }else if(classnums!==0 && buildingname==='none'){
            displaydatas = classdetails.filter(
              (data) => data.itemtype === item && classnumbers[data._id] === classnums
            );
          }else if(classnums!==0 && buildingname!=='none'){
            displaydatas = classdetails.filter(
              (data) => data.itemtype === item && classnumbers[data._id] === classnums && buildingobject[data._id]===buildingname
            );
          }
        }
    

    displaydatas.sort((a,b)=>{
      const dateA=new Date(a.installationdate);
      const dateB=new Date(b.installationdate);
      return order==='newtoold'?dateB-dateA:dateA-dateB;
    });
    
        const tabledatas = displaydatas.map((data) => ({
          key: data._id,
          product:
            data.itemtype === "desktop" ? data.desktopnumber : data.itemtype,
          date: new Date(data.installationdate).toLocaleDateString(),
          description:
            data.description +' '+
            (data.desktopnumber ? ` desktopnumber=${data.desktopnumber}` : "") +' '+
            (data.ipaddress ? ` ipaddress=${data.ipaddress}` : "") +' '+
            (data.monitornumber ? ` monitornumber=${data.monitornumber}` : "") +' '+
            (data.monitorname ? ` monitorname=${data.monitorname}` : "") +' '+
            (data.mousenumber ? ` mousenumber=${data.mousenumber}` : "") +' '+
            (data.mousename ? ` mousename=${data.mousename}` : "") +' '+
            (data.keyboardnumber
              ? ` keyboardnumber=${data.keyboardnumber}`
              : "") +' '+
            (data.keyboardname ? ` keyboardname=${data.keyboardname}` : "") +' '+
            (data.cpunumber ? ` cpunumber=${data.cpunumber}` : "") +' '+
            (data.cpuname ? ` cpuname=${data.cpuname}` : ""),
          classname: allclassname[data._id],
        }));
        setDisplaydata(tabledatas);  
      };
      
    const columns =
        classname === "all"
          ? [
              { title: "DESKTOP NUMBER/ITEM NAME", dataIndex: "product", key: "product" },
              { title: "INSTALLATION DATE", dataIndex: "date", key: "date" },
              {
                title: "DESCRIPTION/DESKTOP INFO.",
                dataIndex: "description",
                key: "description",
              },
              { title: "CLASS NAME", dataIndex: "classname", key: "classname" },
            ]
          : [
              {
                title: "ITEM NAME/DESKTOP NUMBER",
                dataIndex: "product",
                key: "product",
              },
              { title: "INSTALLATION DATE", dataIndex: "date", key: "date" },
              {
                title: "DESCRIPTION",
                dataIndex: "description",
                key: "description",
              },
              
            ];
    const itemchange=(value)=>{
    setItem(value);
    //tabledata(item,labnums);
    }
    
    const classnamechange=(value)=>{
      setClassname(value);
     // tabledata(item,labnums);
    }
    useEffect(()=>{
    tabledata(item,classname,sortorder,allclassname);
    },[item,classname,sortorder,allclassname]);

  return (
    <>
    <Layout2> <div style={{alignItems:'center',justifyContent:'center',display:'flex',fontSize:'24px',fontFamily:'bold'}}><p>CLASS SUMMARY</p></div>
  <div className="d-flex justify-content-center align-items-center gap-2" style={{width:"100%"}}>
  <span>SELECT ITEM : </span>
          <Select placeholder="choose an item" onChange={itemchange}  style={{width:"150px"}}>
          
          <Option value="projector">Projector</Option>
        <Option value="printer">Printer</Option>
        <Option value="speaker">Speaker</Option>
        <Option value="microphone">Microphone</Option>
        <Option value="smartboard">Smartboard</Option>
        <Option value="other">Other</Option>
            <Option value="all">All</Option>
          </Select>
          <span>SELECT CLASS : </span>
           <Select placeholder="choose class number" onChange={classnamechange}  style={{width:"150px"}}>
           <Option value='all' >all</Option>
           { totalclass.map((data)=>(
            <Option value={data}>{data}</Option>
           ))}

           </Select>
           
           <span>SORT ORDER</span>
           <Select placeholder='select sort order' onChange={(value)=>{setSortorder(value)}}>
            <Option value='newtoold'>New to Old</Option>
            <Option value='oldtonew'>Old to New</Option>
           </Select>
         
        </div>
        <button onClick={()=>{window.print()}} style={{marginLeft:'5px',borderRadius:"10px",cursor:'pointer' }}>PRINT</button>
        <Table columns={columns} dataSource={displaydata} pagination={false} />
      
    </Layout2>
      
    </>
  )
}

export default Classsum;
