import React, { useEffect, useState } from "react";
import Layout from "../../components/Layout";
import { Form, Input, Row, Col, message } from "antd";
import axios from "axios";
import "../../styles/Techpagestyles.css";
import { useParams,useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { setRole } from "../../redux/features/roleslice";
 
 const Teachereditnon = () => {
 const params = useParams();
  const navigate = useNavigate();
  const [teacherinfo, setTeacherinfo] = useState(null);
  const roles = useSelector((State) => State.role.role);
  const dispatch = useDispatch();

    useEffect(() => {
      if (roles === "admin" || roles === "editor") {
        localStorage.setItem("role", roles);
      }
    }, [roles]);
  
    useEffect(() => {
      const savedRole = localStorage.getItem("role");
      if (savedRole && savedRole !== roles) {
        dispatch(setRole(savedRole)); // Sync Redux with localStorage
      }
    }, [dispatch]);
    
    const getteacherdata = async () => {
        const res = await axios.post(
          "/api/v1/tech/teacher/getteacherdata",
          {
            teacherid: params.teacherid,
          },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        if (res.data.success) {
          setTeacherinfo(res.data.data);
          //   Form.setFieldsValue(res.data.data);
          //console.log(labdata);
          message.success(res.data.message);
        } else {
          message.error(res.data.message);
        }
      };
    
      useEffect(() => {
        // eslint-disable-next-line react-hooks/exhaustive-deps
        getteacherdata();
      }, []);

      const handleform = async (values) => {
        values.teacherid = params.teacherid;
        try {
          const res = await axios.post("/api/v1/tech/teacher/editteacher", values, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          });
    
          if (res.data.success) {
            message.success(res.data.message);
            navigate("/teachern");
          } else {
            message.error(res.data.message);
          }
          window.location.reload();
        } catch (error) {
          console.log(error);
          message.error("something went wrong");
        }
      };

   return (
     <>
     <Layout>
        {teacherinfo &&
        <div className="form-div">
                  <Form layout="vertical" onFinish={handleform} className="m-2" initialValues={teacherinfo}>
                    <h3 className="text-center">Edit Teacher</h3>
                    <Row gutter={20}>
                      <Col xs={24} md={24} lg={5}>
                        <Form.Item
                          label="Teacher name"
                          name="teachername"
                          rules={[{ required: true }]}
                        >
                          <Input type="text" placeholder="type teacher's name" />
                        </Form.Item>
                      </Col>
                      <Col xs={24} md={24} lg={5}>
                        <Form.Item
                          label="Room number"
                          name="roomno"
                          rules={[{ required: true }]}
                        >
                          <Input type="number" placeholder="type room number" />
                        </Form.Item>
                      </Col>
                      <Col xs={24} md={24} lg={5}>
                        <Form.Item
                          label="Building name"
                          name="buildingname"
                          rules={[{ required: true }]}
                        >
                          <Input type="text" placeholder="type building name" />
                        </Form.Item>
                      </Col>
                      <Col xs={24} md={24} lg={5}>
                        <Form.Item
                          label="Floor number"
                          name="floorno"
                          rules={[{ required: true }]}
                        >
                          <Input type="number" placeholder="type floor number" />
                        </Form.Item>
                      </Col>
                      <Col xs={24} md={24} lg={4}>
                        <button
                          className="btn btn-primary"
                          style={{ width: "180px", marginTop: "25px" }}
                          type="submit"
                        >
                          Submit
                        </button>
                      </Col>
                    </Row>
                  </Form>
                </div>
 }
     </Layout>
       
     </>
   )
 }
 
 export default Teachereditnon
 